import React from 'react'
import { allPass, propEq, ifElse, always } from 'ramda'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import { t } from 'i18next'
import './BotClosableMessage.scss'

function BotClosableMessage (props) {
  const shouldShowMessage = allPass([
    propEq('isOliviaBotChannel', true),
    propEq('isFirstMessage', true),
    propEq('isBotDefaultMessageClosed', false)
  ])

  const handleClose = () => props.closeMessage()

  const message = () => (
    <div className='bot-closable-message'>
      <div className='bot-closable-message__text'>{parseHtmlWithoutSpan(t(`chat.botClosableMessage.${props.userRole}`))}</div>
      { !props.disableClose && <i onClick={handleClose} className='bot-closable-message__close icon-im-close' /> }
    </div>
  )

  return ifElse(
    shouldShowMessage,
    message,
    always(null)
  )(props)
}

export default BotClosableMessage



