import React, { useEffect, useState } from 'react'
import CandidateCardConnector from 'Chat/connectors/Components/CandidateCard/CandidateCardConnector'
import { map, prop } from 'ramda'
import './ShortlistedApplicantsList.scss'

function ShortlistedApplicantsList (props) {
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const candidateIds = map(prop('candidateId'))(props.shortlistedCandidates)
    async function fetchCandidates () {
      await props.fetchMultipleCandidates(candidateIds)
    }
    fetchCandidates()
    setLoading(false)
  }, [])

  const renderCandidateCard = ids => (
    <CandidateCardConnector
      key={ids.candidateId}
      loading={loading}
      jobOfferId={props.jobOfferId}
      candidateId={ids.candidateId}
      applicationId={ids.applicationId}
    />
  )
  const renderList = map(renderCandidateCard)

  return (
    <div className='shortlisted-applicants-list'>
      {renderList(props.shortlistedCandidates)}
    </div>
  )
}

export default ShortlistedApplicantsList



