import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import ChatViewConnector from 'Chat/connectors/Views/ChatViewConnector'
import Loader from 'Shared/components/Loader/Loader'

const ChatApp = ({ t, isLoggedIn }) => isLoggedIn
  ? <ChatViewConnector />
  : <Loader className='loader--app' description={`${t('common.loading')}...`} />
export default withTranslations(ChatApp)



