import React, { useState, useEffect } from 'react'
import ReactHtmlParser from 'react-html-parser'
import Button from 'Shared/components/Button/index'
import { t } from 'i18next'
import cx from 'classnames'
import { messageAnswers } from 'Chat/constants/bot'
import { ConfirmPopup } from 'Shared/components/Popup'
import { cond, T, equals, always } from 'ramda'
import { isNotNil } from 'Shared/helpers/Ramda'
import './MessageYesOrNoAnswer.scss'

function MessageYesOrNoAnswer (props) {
  const getMessageAnswer = () => cond([
    [equals(props.isYesAnswerExisting), always(messageAnswers.yes)],
    [equals(props.isNoAnswerExisting), always(messageAnswers.no)],
    [T, always(null)]
  ])(true)

  const [loading, setLoading] = useState(false)
  const [selectedAnswer, setSelectedAnswer] = useState(getMessageAnswer())

  useEffect(() => {
    setSelectedAnswer(getMessageAnswer())
  }, [props.isYesAnswerExisting, props.isNoAnswerExisting])

  const buttonClass = answer => cx({
    'message-yes-or-no-answer__button': true,
    'message-yes-or-no-answer__button--selected': answer === selectedAnswer,
    'message-yes-or-no-answer__button--no': messageAnswers.no === selectedAnswer,
    'message-yes-or-no-answer__button--yes': messageAnswers.yes === selectedAnswer,
    'message-yes-or-no-answer__button--closed': props.isChannelClosedByOlivia
  })

  const shouldDisableButtons = isNotNil(selectedAnswer) || loading || props.isChannelClosedByOlivia

  const handleAccept = () => {
    setLoading(true)
    props.setIsOliviaTyping()
    setSelectedAnswer(messageAnswers.yes)
    props.onAccept()
    setLoading(false)
  }

  const handleReject = () => {
    setLoading(true)
    props.setIsOliviaTyping()
    setSelectedAnswer(messageAnswers.no)
    props.onReject()
    setLoading(false)
  }

  const DeclineButton = (onClick = () => {}) => (
    <Button
      withIcon
      iconClassName='icon-im-decline-outline'
      className={buttonClass(messageAnswers.no)}
      disabled={shouldDisableButtons}
      onClick={onClick}
    >
      {t('common.no')}
    </Button>
  )

  const ConfirmDecline = () => (
    <ConfirmPopup
      withSubmit
      withCancel
      cancelName={t('common.no')}
      submitName={t('common.yes')}
      onSubmit={handleReject}
      position='top'
      align='left'
      triggerButton={DeclineButton()}
      disableSubmit={shouldDisableButtons}
      className='message-yes-or-no-answer__button'
    >
      {props.withConfirmDeclineWithDescription ? ReactHtmlParser(t('chat.botDeclineWithDescriptionConfirm')) : t('chat.botDeclineConfirm')}
    </ConfirmPopup>
  )

  const showDeclineButton = props.withConfirmDecline ? ConfirmDecline() : DeclineButton(handleReject)

  return (
    <div className='message-yes-or-no-answer__container'>
      <Button
        withIcon
        iconClassName='icon-im-accept-outline'
        className={buttonClass(messageAnswers.yes)}
        disabled={shouldDisableButtons}
        onClick={handleAccept}
      >
        {t('common.yes')}
      </Button>
      { showDeclineButton }
    </div>
  )
}

export default MessageYesOrNoAnswer



