import APIInterceptor from './APIInterceptor'

export default class JobAlertsService {
  constructor (api = APIInterceptor) {
    this.api = api
  }

  sendMessage (messageType, signature, jobOfferId, jobOfferApplicationId) {
    return this.api.post('/olivia/send-message', { messageType, signature, jobOfferId, jobOfferApplicationId })
  }
}



