import React, { Component, Fragment } from 'react'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'
import withTranslations from 'Shared/hoc/withTranslations'
import { Roles } from 'Shared/constants/User'
import PropTypes from 'prop-types'
import InterviewRequestConnector from 'Chat/connectors/Components/InterviewRequest/InterviewRequestConnector'
import { InterviewInvitationStatuses } from 'Shared/constants/Interviews'
import { nl2br, getFirstWord } from 'Shared/helpers/string'
import './Message.scss'
import moment from 'moment'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import MessageYesOrNoAnswerConnector from 'Chat/connectors/Components/MessageYesOrNoAnswer/MessagesYesOrNoAnswerConnector'
import ShortlistedApplicantsListConnector from 'Chat/connectors/Components/ShortlistedApplicantsList/ShortlistedApplicantsListConnector'
import TypingPlaceholderConnector from 'Chat/connectors/Components/TypingPlaceholder/TypingPlaceholderConnector'
import { TIMEOUT_FOR_TYPING_ANIMATION } from 'Chat/constants/bot'

class MessageReceiver extends Component {
  static propTypes = {
    interviewRequest: PropTypes.bool.isRequired,
    interviewInvitationStatus: PropTypes.string,
    isAnyTimeSlotChosen: PropTypes.bool,
    isRespondAChangeRequest: PropTypes.bool,
    chosenTimeSlot: PropTypes.object,
    scrollToBottom: PropTypes.func
  }

  state = {
    isOliviaTyping: this.props.isLastMessage && this.props.isOliviaTyping
  }

  stopOliviaTyping = () => {
    this.props.stopOliviaTyping()
    this.setState({ isOliviaTyping: false })
  }

  componentDidMount () {
    if (this.props.isLastMessage) {
      this.props.updateLastViewedMessage(this.props.messageIndex)
    }

    if (this.props.isLastMessage && this.props.isOliviaTyping) {
      setTimeout(this.stopOliviaTyping, TIMEOUT_FOR_TYPING_ANIMATION)
    }
  }

  showInterviewRespond = () => {
    const { t, interviewInvitationStatus, isAnyTimeSlotChosen, chosenTimeSlot, isRespondAChangeRequest } = this.props

    switch (true) {
      case interviewInvitationStatus === InterviewInvitationStatuses.rejected:
        return <div><i className='icon-im-decline-outline message__interview-icon--decline' />&nbsp;{t('interviewInviteResponds.reject')}</div>
      case interviewInvitationStatus === InterviewInvitationStatuses.accepted && !isAnyTimeSlotChosen:
        return <div><i className='icon-im-accept-outline message__interview-icon--accept' />&nbsp;{t('interviewInviteResponds.accept')} </div>
      case interviewInvitationStatus === InterviewInvitationStatuses.accepted && isAnyTimeSlotChosen:
        return <div><i className='icon-im-accept-outline message__interview-icon--accept' />&nbsp;
          {t('interviewInviteResponds.accept')}&nbsp;{!isRespondAChangeRequest &&
          <span className='message__interview--time'>
            <i className='ion-md-time' />&nbsp;
            {moment(chosenTimeSlot.dateFrom).utc().format('LL')},&nbsp;
            {moment(chosenTimeSlot.dateFrom).utc().format('hh:mm:A')}&nbsp;-&nbsp;{moment(chosenTimeSlot.dateTo).utc().format('hh:mm:A')}</span>}
        </div>
      default:
        return null
    }
  }

  showInterviewInvitation = () => {
    const { t, interviewInvitationStatus, interviewRequest, scrollToBottom } = this.props
    const hasRespondedToInvitation = interviewInvitationStatus === InterviewInvitationStatuses.rejected ||
      interviewInvitationStatus === InterviewInvitationStatuses.accepted

    switch (true) {
      case interviewRequest && hasRespondedToInvitation:
        return <div className='message__content--interview-invitation'>
          {this.showInterviewRespond()}
        </div>
      case interviewRequest && !hasRespondedToInvitation:
        return <div className='message__content--interview-invitation'>
          <div className='time-slots-interview-request__header'>{t('chat.interviewRequest')}</div>
          <div className='message__content--invitation-respond'>
            <InterviewRequestConnector scrollToBottom={scrollToBottom} />
          </div>
        </div>
      default:
        return null
    }
  }

  getAvatarDetails = () => ({
    firstName: this.props.authorFriendlyName,
    photoUrl: this.props.authorPhotoUrl,
    avatarColour: this.props.avatarColour
  })

  render () {
    const {
      messageContent,
      messageTimestamp,
      authorFriendlyName,
      userRole,
      isCandidatesMessage,
      isOliviaBotUser,
      messageRequiresYesOrNoAnswer,
      botMessageType,
      messageSid,
      jobOfferId,
      signature,
      applicationId,
      messageWithShortlistedCandidatesList,
      isChannelClosedByOlivia
    } = this.props

    return this.state.isOliviaTyping ? <TypingPlaceholderConnector scrollToBottom={this.props.scrollToBottom} /> : (
      <Fragment>
        <div className='message message--receiver'>
          {
            !isOliviaBotUser && (
              <UserAvatar details={this.getAvatarDetails()} />
            )
          }
          <div className='message__receiver__wrapper'>
            <div className='message__datetime'>
              { !isCandidatesMessage && <span className='message__author'>{ getFirstWord(authorFriendlyName) }, </span> }
              { formatDate(messageTimestamp, formats.time) }
            </div>
            <div className='message__content message__content--receiver'>
              { parseHtmlWithoutSpan(nl2br(messageContent)) }
              {userRole === Roles.candidate.name && this.showInterviewInvitation()}
              {messageRequiresYesOrNoAnswer && (
                <MessageYesOrNoAnswerConnector
                  signature={signature}
                  jobOfferId={jobOfferId}
                  messageSid={messageSid}
                  botMessageType={botMessageType}
                  applicationId={applicationId}
                  isChannelClosedByOlivia={isChannelClosedByOlivia}
                />
              )}
              {messageWithShortlistedCandidatesList && <ShortlistedApplicantsListConnector messageSid={messageSid} jobOfferId={jobOfferId} />}
            </div>
          </div>
        </div>
      </Fragment>
    )
  }
}

export default withTranslations(MessageReceiver)



