import React from 'react'
import { Link } from 'react-router-dom'
import './CandidateCard.scss'
import UserAvatar from 'Shared/components/UserAvatar'
import cx from 'classnames'

function CandidateCard (props) {
  const cardClass = cx({
    'candidate-card': true,
    'candidate-card--loading': props.loading
  })

  return (
    <Link to={`/employer/job-offer/${props.jobOfferId}/applicant/${props.applicationId}`} target='_blank' className={cardClass}>
      <UserAvatar className='candidate-card__avatar' details={props.details} />
      <div className='candidate-card__full-name'>{props.fullName}</div>
      <div className='candidate-card__majors'>{props.majors}</div>
      <div className='candidate-card__location'>{props.location}</div>
    </Link>
  )
}

export default CandidateCard



