import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import Button from 'Shared/components/Button'
import heroImage from 'Shared/assets/images/illustrations/oliv-plus-main-image.svg'
import * as R from 'ramda'
import './OlivPlusBanner.scss'
import ModalPlayer from 'Shared/components/ModalPlayer/ModalPlayer'
import { t } from 'i18next'
import appRegistry from 'Shared/services/AppRegistry'
import { threeDaysPassed } from 'Shared/helpers/date'
import { LocalStorageKeys } from 'Shared/constants/Storage'
import { OLIV_PLUS_VIDEO_URL } from 'Shared/constants/Global'
import mixpanel from 'mixpanel-browser'

const OlivPlusBanner = ({ hideCloseMark, chatMobileView, hasMembership, isApproved, approvedAt, history, isChat, formattedPrice }) => {
  const [isVisible, setVisiblility] = useState(true)

  const hideBanner = () => {
    appRegistry.storage.set(LocalStorageKeys.olivPlusBannerStatus, 'closed')
    setVisiblility(false)
  }

  const containerClass = cx({
    'oliv-plus-banner__container': !chatMobileView,
    'oliv-plus-chat-banner__mobile-container': chatMobileView
  })

  useEffect(() => {
    if (!isChat && !chatMobileView) {
      const wasManuallyClosed = appRegistry.storage.get(LocalStorageKeys.olivPlusBannerStatus)
      setVisiblility(!hasMembership && isApproved && !threeDaysPassed(approvedAt) && R.isNil(wasManuallyClosed))
    }
  })

  if (!isVisible) return null

  return (
    <div className={containerClass}>
      {!hideCloseMark && <i className='icon-im-close oliv-plus-banner__close-mark' onClick={hideBanner} />}
      <div className='columns'>
        {chatMobileView && <img className='oliv-plus-banner__mobile-chat-image' src={heroImage} alt='Hero Image' />}
        <div className='column col-6 col-sm-12'>
          <div className='oliv-plus-banner__heading-container'>
            <div className='oliv-plus-banner__heading'>
              {t('olivPlus.welcomeSection.header')}
            </div>
            <div className='oliv-plus-banner__description'>
              <ul className='oliv-plus-banner__description--features-list'>
                <li className='oliv-plus-banner__description--features-list-item'>
                  <i className='icon-im-done oliv-plus-banner__description--features-list-icon' />
                  {t('olivPlus.benefitsSection.boostApplicationsTitle')}
                </li>
                <li className='oliv-plus-banner__description--features-list-item'>
                  <i className='icon-im-done oliv-plus-banner__description--features-list-icon' />
                  {t('olivPlus.benefitsSection.messageEmployersTitle')}
                </li>
              </ul>
            </div>
            <div className='column col-12 col-sm-12 col-md-12 oliv-plus-banner__button-container'>
              <Button
                onClick={() => {history.push('/candidate/oliv-plus/review'); mixpanel.track("Button Click - Get Oliv Plus");}}
                className='button button--primary oliv-plus-banner__submit-input-btn'
              >
                {t('olivPlus.welcomeSection.getOlivPlus', { formattedPrice: formattedPrice })}
              </Button>
            </div>
            <div className='oliv-plus-banner__hint-container'>
              <div className='oliv-plus-banner__hint-content'>
                <span>{t('olivPlus.welcomeSection.howItWorks')}</span>
                <ModalPlayer
                  videoUrl={OLIV_PLUS_VIDEO_URL}
                  customTrigger={
                    <p className='oliv-plus-banner__hint--video-trigger'>{t('olivPlus.welcomeSection.watchVideo')}</p>
                  }
                />
              </div>
            </div>
          </div>
        </div>
        <div className='column col-6 col-sm-12'>
          <div className='oliv-plus-banner__image-container'>
            <img className='oliv-plus-banner__image' src={heroImage} alt='Hero Image' />
          </div>
        </div>
      </div>
    </div>
  )
}

export default withRouter(withTranslations(OlivPlusBanner))



