import { connect } from 'react-redux'
import { Roles } from 'Shared/constants/User'
import ChannelsListFilters from 'Chat/components/ChannelsListFilters'
import { channelFiltersEmployer, channelFiltersCandidate } from 'Chat/constants/channel'

const mapStateToProps = state => {
  const { role } = state.user
  const optionsMap = {
    [Roles.candidate.name]: channelFiltersCandidate(),
    [Roles.employer.name]: channelFiltersEmployer()
  }

  return ({
    options: optionsMap[role]
  })
}

export default connect(mapStateToProps)(ChannelsListFilters)



