import React, { Fragment, PureComponent } from 'react'
import { compose } from 'redux'
import MessagesLoader from 'Chat/components/MessagesLoader/MessagesLoader'
import MessagesViewConnector from 'Chat/connectors/Views/MessagesViewConnector'
import { Route } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import MessagesEmpty from 'Chat/components/MessagesEmpty/MessagesEmpty'
import Loader from 'Shared/components/Loader/Loader'
import ChannelsListConnector from 'Chat/connectors/Components/ChannelsList/ChannelsListConnector'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import withChatViewWrapper from './withChatViewWrapper'

import './ChatView.scss'
import { Helmet } from 'react-helmet'
import { Roles } from 'Shared/constants/User'
import OlivPlusBannerConnector from 'Candidate/connectors/OlivPlus/OlivPlusBannerConnector'
import OliviaBotBannerConnector from 'Employer/connectors/OliviaBotBanner/OliviaBotBannerConnector'
import mixpanel from 'mixpanel-browser'

class ChatView extends PureComponent {
  componentDidMount() {
    const { userRole } = this.props;
    mixpanel.track(`Page View - Chat - ${userRole.charAt(0).toUpperCase() + userRole.slice(1)}`);
  }
  render () {
    const { t, sortedChannels, userRole, noChannelSelected, isCandidateApproved, hasOlivPlusMembership, neverHadAnyJobMembership } = this.props
    const isCandidate = userRole === Roles.candidate.name
    const hasNoChannels = sortedChannels.length === 0

    const generateChannelContent = () => {
      switch (true) {
        case hasNoChannels && isCandidate && !hasOlivPlusMembership && isCandidateApproved:
          return <OlivPlusBannerConnector hideCloseMark isChat />
        case !isCandidate && noChannelSelected && !neverHadAnyJobMembership:
          return <OliviaBotBannerConnector className='chat-view__olivia-banner' disableClose />
        case hasNoChannels && noChannelSelected:
          return <MessagesEmpty isMessagesVisible={this.props.isMessagesViewVisible} text={t(`chat.selectChat`)} />
        case hasNoChannels:
          return <MessagesEmpty isMessagesVisible={this.props.isMessagesViewVisible} text={t(`chat.messagesEmptyState.${userRole}`)} />
        default:
          return null
      }
    }

    return (
      <Fragment>
        <Helmet>
          <title>{t('meta.candidate.title.chat')}</title>
          <meta property='og:title' content={t('meta.candidate.title.chat')} />
        </Helmet>
        <PageTopHeaderMobile title={t('mobileBottomNavigation.messages')} />
        {
          this.props.loadingChannels ? <Loader className='loader--app' description={`${t('common.loading')}...`} /> : (
            <div className='chat-view__container'>
              <div className='chat-view__channels'>
                <div className='chat-view__header--channels'>
                  <span className='chat-view__header--title'>{t('chat.recentMessages')}</span>
                </div>
                <ChannelsListConnector />
              </div>
              <div className='chat-view__channel-content'>
                {generateChannelContent()}
                <Route path={`/${userRole}/chat/:channelSid`} component={this.props.loadingChannels ? MessagesLoader : MessagesViewConnector} />
              </div>
            </div>
          )
        }
      </Fragment>
    )
  }
}

export default compose(withChatViewWrapper, withTranslations)(ChatView)



