import { connect } from 'react-redux'
import MessagesView from 'Chat/views/MessagesView'
import { withRouter } from 'react-router-dom'
import { showChannelMessages } from 'Chat/ducks/chat'
import { fetchMessages, sendMessage } from 'Chat/ducks/classes/messages'
import { getMessagesByChannelSid } from 'Chat/selectors/messages'
import {
  getChannelAvatarFromTwilio,
  hasAttributesInTwilio,
  getChannelAvatarFromDatabase,
  getChannelNameFromTwilio,
  getChannelNameFromDatabase,
  getChannelApplicationId,
  isChannelDismissed,
  getChannelMessagesCount,
  isChannelExisting,
  getChannelAvatarColourFromTwilio,
  getChannelAvatarColourFromDatabase,
  isCandidateAwaitingForEmployersReply,
  isOliviaBotChannel
} from 'Chat/selectors/channels'
import { isMessagesViewVisible } from 'Chat/selectors/view'
import { fetchJobOfferApplicationAllDetails } from 'Shared/ducks/jobOfferApplications'

const mapStateToProps = (state, ownProps) => {
  const { role, details: { id }, impersonation } = state.user
  const { channelSid } = ownProps.match.params
  const hasAttributesInTwilioSet = hasAttributesInTwilio(state, channelSid)

  if (!isChannelExisting(state, channelSid)) {
    return ownProps.history.push(`/${role}/chat`)
  }

  return ({
    impersonation,
    channelSid,
    userId: id,
    userRole: role,
    messagesCount: getChannelMessagesCount(state, channelSid),
    applicationId: getChannelApplicationId(state, channelSid),
    dismissed: isChannelDismissed(state, channelSid),
    messages: getMessagesByChannelSid(channelSid)(state),
    channelAvatar: hasAttributesInTwilioSet
      ? getChannelAvatarFromTwilio(state, channelSid)
      : getChannelAvatarFromDatabase(state, channelSid),
    avatarColour: hasAttributesInTwilioSet
      ? getChannelAvatarColourFromTwilio(state, channelSid)
      : getChannelAvatarColourFromDatabase(state, channelSid),
    channelName: hasAttributesInTwilioSet
      ? getChannelNameFromTwilio(state, channelSid)
      : getChannelNameFromDatabase(state, channelSid),
    isMessagesViewVisible: isMessagesViewVisible(state),
    isCandidateAwaitingForEmployersReply: isCandidateAwaitingForEmployersReply(state, channelSid),
    isOliviaBotChannel: isOliviaBotChannel(state, channelSid)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const { channelSid } = ownProps.match.params

  return ({
    fetchApplication: applicationId => dispatch(fetchJobOfferApplicationAllDetails(applicationId)),
    fetchMessages: (sid = channelSid) => dispatch(fetchMessages(sid, { pageSize: 30 })),
    loadMoreMessages: oldestMessageIndex => dispatch(fetchMessages(channelSid, { pageSize: 30, anchor: oldestMessageIndex })),
    sendMessage: message => dispatch(sendMessage(channelSid, message)),
    showChannelMessages: () => dispatch(showChannelMessages())
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MessagesView))



