import React from 'react'
import { formatDayTimelineDate } from 'Shared/helpers/date'
import './DayTimeline.scss'

const DayTimeline = ({ isFirstMessageOfTheDay, date }) => isFirstMessageOfTheDay && (
  <div className='day-timeline'>
    <div className='day-timeline__line' />
    <div className='day-timeline__date'>{formatDayTimelineDate(date)}</div>
  </div>
)

export default DayTimeline



