import React, { useState, useEffect } from 'react'
import { BigWithIconButton } from 'Shared/components/Button'
import { ConfirmPopup } from 'Shared/components/Popup'
import { t } from 'i18next'
import RadioWrapper from 'Shared/components/FormElements/RadioWrapper/RadioWrapper'
import Radio from 'Shared/components/FormElements/Radio/Radio'
import { mapIndexed } from 'Shared/helpers/Ramda'
import './ChannelsListFilters.scss'

function ChannelsListFilters (props) {
  const [value, setValue] = useState('all')

  useEffect(() => {
    props.getSelectedValue(value)
  }, [])

  useEffect(() => {
    props.getSelectedValue(value)
  }, [value])

  const triggerButton = () => (
    <BigWithIconButton className='chat-list-filters__button'>
      <i className='icon-im-filters' />
      {t('common.filterBy')}
      {value !== 'all' && <div className='chat-list-filters__button--counter'>1</div>}
    </BigWithIconButton>
  )

  const radioOption = (option, index) => {
    const radioProps = {
      input: {
        onChange: () => setValue(option.value),
        value
      },
      meta: { touched: false, error: false, submitting: false }
    }

    return (
      <div className='chat-list-filters__radio-option'>
        <Radio
          key={`filter-option-${index}`}
          {...radioProps}
          radioButtonValue={option.value}
          text={option.label}
        />
      </div>
    )
  }

  const radioOptionList = () => mapIndexed(radioOption)(props.options)

  return (
    <div className='chat-list-filters__container'>
      <ConfirmPopup
        position='bottom'
        align='left'
        triggerButton={triggerButton()}
      >
        <div className='chat-list-filters__radio-wrapper'>
          <RadioWrapper>
            {radioOptionList()}
          </RadioWrapper>
        </div>
      </ConfirmPopup>
    </div>
  )
}

export default ChannelsListFilters



