import { connect } from 'react-redux'
import OlivPlusBanner from 'Candidate/components/OlivPlusBanner/OlivPlusBanner'
import { getCandidateProfileApprovalDate, isCandidateProfileApproved } from 'Shared/selectors/user'
import { hasOlivPlusMembership } from 'Shared/selectors/membership'
import { getPriceWithCurrency } from 'Shared/helpers/payment'
import { getOlivPlusBasePrice, getPaymentCurrency } from 'Shared/selectors/geolocation'

const mapStateToProps = state => ({
  hasMembership: hasOlivPlusMembership(state),
  isApproved: isCandidateProfileApproved(state),
  approvedAt: getCandidateProfileApprovalDate(state),
  formattedPrice: getPriceWithCurrency(getOlivPlusBasePrice(state), getPaymentCurrency(state))
})

export default connect(mapStateToProps)(OlivPlusBanner)



