import { connect } from 'react-redux'
import { Channel } from 'Chat/components/Channel'
import { withRouter } from 'react-router-dom'
import { showChannelMessages } from 'Chat/ducks/chat'
import { receiveChannelDismissStatusChange } from 'Chat/ducks/classes/channels'
import { getChannelMembers } from 'Chat/ducks/classes/members'
import { getUserDescriptors } from 'Chat/ducks/classes/users'
import { getChannelLastMessage } from 'Chat/selectors/messages'
import { Roles } from 'Shared/constants/User'
import { fetchOrganisationIfNeeded } from 'Shared/ducks/organisations'
import { fetchCandidateIfNeeded } from 'Shared/ducks/candidates'
import {
  getChannelBySid,
  getChannelAvatarFromTwilio,
  hasAttributesInTwilio,
  getChannelAvatarFromDatabase,
  getChannelNameFromTwilio,
  getChannelNameFromDatabase,
  getChannelAvatarColourFromTwilio,
  getChannelAvatarColourFromDatabase,
  isOliviaBotChannel
} from 'Chat/selectors/channels'
import chatService from 'Chat/services/ChatService'
import { SHORTLIST_BOT_NAME } from 'Shared/constants/Global'

const mapStateToProps = (state, ownProps) => {
  const channel = getChannelBySid(state, ownProps.channelSid)
  const isOliviaBot = isOliviaBotChannel(state, ownProps.channelSid)
  const hasAttributesInTwilioSet = hasAttributesInTwilio(state, ownProps.channelSid) || isOliviaBot
  const findChannelNameForNonBotChannel = hasAttributesInTwilioSet
    ? getChannelNameFromTwilio(state, ownProps.channelSid)
    : getChannelNameFromDatabase(state, ownProps.channelSid)

  return ({
    userId: state.user.details.id,
    lastMessage: getChannelLastMessage(ownProps.channelSid)(state),
    channel,
    isOliviaBotChannel: isOliviaBot,
    isSelected: ownProps.location.pathname.includes(ownProps.channelSid),
    hasAttributesInTwilio: hasAttributesInTwilio(state, ownProps.channelSid),
    channelAvatar: hasAttributesInTwilioSet
      ? getChannelAvatarFromTwilio(state, ownProps.channelSid)
      : getChannelAvatarFromDatabase(state, ownProps.channelSid),
    avatarColour: hasAttributesInTwilioSet
      ? getChannelAvatarColourFromTwilio(state, ownProps.channelSid)
      : getChannelAvatarColourFromDatabase(state, ownProps.channelSid),
    channelName: isOliviaBot ? SHORTLIST_BOT_NAME : findChannelNameForNonBotChannel
  })
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const fetchDetails = channel => {
    const { attributes: { candidateId, organisationId } } = channel
    const detailsMap = {
      fetchDetails: {
        [Roles.candidate.name]: () => dispatch(fetchOrganisationIfNeeded(organisationId)),
        [Roles.employer.name]: () => dispatch(fetchCandidateIfNeeded(candidateId))
      }
    }
    return detailsMap.fetchDetails[ownProps.userRole]()
  }

  return ({
    receiveChannelDismissStatusChange: () => chatService.receiveChannelDismissStatusChange(ownProps.channelSid, updatedChannel => dispatch(receiveChannelDismissStatusChange(updatedChannel))),
    showChannelMessages: () => dispatch(showChannelMessages()),
    getChannelMembers: () => dispatch(getChannelMembers(ownProps.channelSid)),
    getUserDescriptors: () => dispatch(getUserDescriptors(ownProps.channelSid)),
    fetchDetails
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Channel))



