import React from 'react'
import './MessagesEmpty.scss'
import cx from 'classnames'

const MessagesEmpty = ({ text, isMessagesVisible }) => {
  const messagesEmptyClass = cx({
    'messages-empty__container': true,
    'messages-empty__container--hide': !isMessagesVisible
  })

  return (
    <div className={messagesEmptyClass}>
      <div className='messages-empty__text-container'>
        <div className='messages-empty__icon'><i className='icon-im-email' /></div>
        <div className='messages-empty__text'>{text}</div>
      </div>
      <div className='messages__send messages__send--empty'>
        <div className='messages__send-form'>
          <input disabled className='messages__send-input' />
          <button disabled className='message__send-button message__send-button--loading'>
            <i className='icon-im-send' />
          </button>
        </div>
      </div>
    </div>
  )
}

export default MessagesEmpty



