import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import MessageYesOrNoAnswer from 'Chat/components/MessageYesOrNoAnswer'
import { hasChannelGivenMessageType, getMessagesByChannelSid } from 'Chat/selectors/messages'
import { botMessageTypes, messageAnswersTypes } from 'Chat/constants/bot'
import { cond, T, equals } from 'ramda'
import OliviaService from 'Shared/services/OliviaService'
import { messageTypeWithConfirmDecline, messageTypeWithConfirmDeclineWithDescription } from 'Chat/helpers/bot'
import { updateChannelAttributes } from 'Chat/ducks/classes/channels'
import { enableOlivia } from 'Shared/ducks/jobOffers'

const oliviaService = new OliviaService()

const mapStateToProps = (state, ownProps) => {
  const getYesAnswerType = messageAnswersTypes[ownProps.botMessageType].yes
  const getNoAnswerType = messageAnswersTypes[ownProps.botMessageType].no

  return ({
    messages: getMessagesByChannelSid(ownProps.match.params.channelSid)(state),
    withConfirmDecline: messageTypeWithConfirmDecline(ownProps.botMessageType),
    withConfirmDeclineWithDescription: messageTypeWithConfirmDeclineWithDescription(ownProps.botMessageType),
    isYesAnswerExisting: hasChannelGivenMessageType(state, ownProps.messageSid, getYesAnswerType),
    isNoAnswerExisting: hasChannelGivenMessageType(state, ownProps.messageSid, getNoAnswerType)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  setIsOliviaTyping: () => dispatch(updateChannelAttributes(ownProps.match.params.channelSid, { isOliviaTyping: true })),
  onAccept: () => {
    const handleAcceptEnquire = async () => {
      await oliviaService.sendMessage(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_USAGE_ENQUIRY_EMPLOYER_REPLIED_YES, ownProps.signature)
      return dispatch(enableOlivia(ownProps.jobOfferId))
    }

    const handleAccept = answerMessageType => () => oliviaService.sendMessage(answerMessageType, ownProps.signature, ownProps.jobOfferId, ownProps.applicationId)

    return cond([
      [equals(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_USAGE_ENQUIRY), handleAcceptEnquire],
      [equals(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_1), handleAccept(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_1_ANSWER_YES)],
      [equals(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_1_ANSWER_YES), handleAccept(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_2_ANSWER_YES)],
      [equals(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_2_ANSWER_YES), handleAccept(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_3_ANSWER_YES)],
      [equals(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_3_ANSWER_YES), handleAccept(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_4_ANSWER_YES)],
      [T, () => {}]
    ])(ownProps.botMessageType)
  },
  onReject: () => {
    const handleReject = answerMessageType => () => oliviaService.sendMessage(answerMessageType, ownProps.signature, ownProps.jobOfferId, ownProps.applicationId)

    return cond([
      [equals(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_USAGE_ENQUIRY), handleReject(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_USAGE_ENQUIRY_EMPLOYER_REPLIED_NO)],
      [T, handleReject(botMessageTypes.CHAT_BOT_MESSAGE_TYPE_AUTO_SHORTLIST_ANSWER_NO)]
    ])(ownProps.botMessageType)
  }
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MessageYesOrNoAnswer))



