import React, { useEffect } from 'react'
import { t } from 'i18next'
import './TypingPlaceholder.scss'
import moment from 'moment'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'

function TypingPlaceholder (props) {
  useEffect(() => {
    props.scrollToBottom()
  }, [])

  return props.isOliviaTyping ? (
    <div className='typing-placeholder__container'>
      <div className='typing-placeholder__timestamp'>{formatDate(moment(), formats.time)}</div>
      <div className='typing-placeholder'>
        <div className='typing-placeholder__dot typing-placeholder__dot-1' />
        <div className='typing-placeholder__dot typing-placeholder__dot-2' />
        <div className='typing-placeholder__dot typing-placeholder__dot-3' />
      </div>
      <div className='typing-placeholder__typing-text'>{t('chat.oliviaIsTyping')}</div>
    </div>
  ) : <div />
}

export default TypingPlaceholder



