import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations/index'
import Loader from 'Shared/components/Loader/Loader'
import cx from 'classnames'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import './MessagesLoader.scss'

const MessagesLoader = ({ t, isMobile }) => {
  const loaderClass = cx({
    'messages__loader': true,
    'messages__loader--mobile': isMobile
  })

  return (
    <div className={loaderClass}>
      <div className='messages__loader-text'>
        <Loader />
      </div>
    </div>
  )
}

export default withDeviceWidthDetector(withTranslations(MessagesLoader))



