import React, { Component } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import * as R from 'ramda'
import './MultiSelectList.scss'

class MultiSelectList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      value: []
    }
  }

  componentDidMount () {
    this.setInitialValuesIfNeeded()
  }

  componentDidUpdate (prevProps, prevState) {
    if (prevProps.input.value.length !== this.props.input.value.length) {
      this.setInitialValuesIfNeeded()
    }

    if (prevState.value.length !== this.state.value.length) {
      this.setReduxFormChangeIfNeeded()
    }
  }

  setInitialValuesIfNeeded = () => this.props.withReduxForm && this.setState({ value: this.props.input.value })
  setReduxFormChangeIfNeeded = () => this.props.withReduxForm && this.props.input.onChange(this.state.value)

  isSelected = value => R.includes(value, this.state.value)
  isInitialValue = value => R.includes(value, this.props.initialValues)
  addValue = value => R.append(value, this.state.value)
  removeValue = value => R.reject(R.equals(value), this.state.value)

  selectOption = value => this.setState({ value: this.addValue(value) })
  deselectOption = value => this.setState({ value: this.removeValue(value) })

  onSelectChange = event => {
    return (
      R.ifElse(
        this.isSelected,
        this.deselectOption,
        this.selectOption
      )(event.target.value)
    )
  }

  optionLabel = props => {
    const { customLabel } = this.props
    const CustomLabelComponent = customLabel
    return customLabel ? <CustomLabelComponent {...props} /> : <span className='multiselect-list__option-label'>{props.label}</span>
  }

  option = props => {
    const { input } = this.props
    const disabled = this.isSelected(props.value.toString()) && R.not(this.props.canUncheck) && this.isInitialValue(props.value.toString())
    const checkmarkClass = cx({
      'multiselect-list__option-checkmark': true,
      'multiselect-list__option-checkmark--disabled': disabled
    })

    return (
      <label className='multiselect-list__option' key={`multi-select-option-${props.value}`}>
        {this.optionLabel(props)}
        <input
          className='multiselect-list__option-checkbox'
          type='checkbox'
          value={props.value}
          onChange={this.onSelectChange}
          checked={this.isSelected(props.value.toString())}
          disabled={disabled}
        />
        <span className={checkmarkClass} />
      </label>
    )
  }

  getOptions = () => R.map(
    this.option
  )(this.props.options)

  render () {
    const { className } = this.props
    const containerClass = cx({ [className]: !!className })

    return (
      <div className={containerClass}>
        {this.getOptions()}
      </div>
    )
  }
}

MultiSelectList.defaultProps = {
  options: [],
  initialValues: [],
  withReduxForm: false,
  canUncheck: false
}

MultiSelectList.propTypes = {
  options: PropTypes.array.isRequired,
  initialValues: PropTypes.array,
  customLabel: PropTypes.bool,
  withReduxForm: PropTypes.bool,
  canUncheck: PropTypes.bool
}

export default MultiSelectList

export const MultiSelectListReduxForm = props => <MultiSelectList withReduxForm {...props} />



