import MessagesViewWeb from './MessagesView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import MobileWindow from 'Shared/components/MobileWindow/MobileWindow.mobile'

import React from 'react'

const MessagesView = (props) => props.isMobile ? <MobileWindow show><MessagesViewWeb {...props} /></MobileWindow> : <MessagesViewWeb {...props} />

export default withDeviceWidthDetector(MessagesView)



