import React from 'react'
import { Field, Form } from 'redux-form'
import { MultiSelectListReduxForm } from 'Shared/components/FormElementsMobile/MultiSelectList'
import UserLogoOption from 'Shared/components/FormElementsMobile/MultiSelectList/components/UserLogoOption'

class AddTeamMembersFormMobile extends React.PureComponent {
  componentDidUpdate (prevProps) {
    if (this.props.shouldResetForm !== prevProps.shouldResetForm && this.props.shouldResetForm) {
      this.props.reset()
    }
  }

  render () {
    const { handleSubmit, options, selectedEmployers } = this.props
    return <Form onSubmit={handleSubmit}>
      <Field
        component={MultiSelectListReduxForm}
        options={options}
        name='employers'
        initialValues={selectedEmployers}
        customLabel={UserLogoOption}
      />
    </Form>
  }
}

export default AddTeamMembersFormMobile



