import React from 'react'
import ChannelsListFiltersMobile from './ChannelsListFilters.mobile'
import ChannelsListFiltersWeb from './ChannelsListFilters'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ChannelsListFilters = props => props.isMobile ? <ChannelsListFiltersMobile {...props} /> : <ChannelsListFiltersWeb {...props} />

export default withDeviceWidthDetector(ChannelsListFilters)



