import { connect } from 'react-redux'
import ChannelFilterTag from 'Chat/components/ChannelFilterTag'
import { getChannelTagStyleConfig } from 'Chat/selectors/channels'

const mapStateToProps = (state, ownProps) => ({
  tagName: getChannelTagStyleConfig(state, ownProps.channelSid, 'name'),
  tagColor: getChannelTagStyleConfig(state, ownProps.channelSid, 'color')
})

export default connect(mapStateToProps)(ChannelFilterTag)



