import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { EmployersMessagesHeader } from 'Chat/components/MessagesHeader'
import { getApplicationStatus } from 'Shared/selectors/jobOfferApplications'
import { hideChannelMessages } from 'Chat/ducks/chat'
import { changeChannelDismissStatus } from 'Chat/ducks/classes/channels'
import {
  hasAttributesInTwilio,
  getChannelNameFromTwilio,
  getChannelNameFromDatabase
} from 'Chat/selectors/channels'

const mapStateToProps = (state, ownProps) => {
  const { channelSid } = ownProps.match.params
  const {
    friendlyName,
    attributes: {
      candidateId,
      jobOfferId,
      applicationId,
      dismissed
    }
  } = state.chat.channels.entries[channelSid]

  return ({
    jobOfferTitle: friendlyName,
    candidateName: hasAttributesInTwilio(state, channelSid)
      ? getChannelNameFromTwilio(state, channelSid)
      : getChannelNameFromDatabase(state, channelSid),
    candidateStatus: getApplicationStatus(applicationId)(state),
    applicationId,
    jobOfferId,
    dismissed,
    candidateId
  })
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const { channelSid } = ownProps.match.params
  return ({
    dismissChat: () => dispatch(changeChannelDismissStatus(channelSid, true)),
    unblockChat: () => dispatch(changeChannelDismissStatus(channelSid, false)),
    hideChannelMessages: () => dispatch(hideChannelMessages())
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EmployersMessagesHeader))



