import React, { Component } from 'react'

const withChatViewWrapper = (ComposedComponent) => class HOC extends Component {
  constructor () {
    super()
    this.state = {
      loadingChannels: true
    }
  }

  componentDidMount () {
    this.props.receiveNewChannelMember()
    if (this.props.areChannelsFetched) {
      this.setState({ loadingChannels: false })
    }
  }

  async componentDidUpdate (prevProps, prevState) {
    if (!prevProps.areChannelsFetched && this.props.areChannelsFetched) {
      this.setState({ loadingChannels: false })
    }
  }

  render () {
    return <ComposedComponent {...this.props} loadingChannels={this.state.loadingChannels} />
  }
}

export default withChatViewWrapper



