import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { Field, Form } from 'redux-form'
import TeamMemberMultiSelect from 'Shared/components/FormElements/TeamMemberMultiSelect'

class AddTeamMembersForm extends React.PureComponent {
  render () {
    const { t, handleSubmit, options } = this.props

    return <Form onSubmit={handleSubmit}>
      <Field
        component={TeamMemberMultiSelect}
        label={t('forms.teamMembersConversation.label')}
        name='employers'
        placeholder={t('forms.teamMembersConversation.placeholder')}
        options={options}
      />
    </Form>
  }
}

export default withTranslations(AddTeamMembersForm)



