import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Link, withRouter } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations/index'
import cx from 'classnames'
import applicantStatuses from 'Shared/constants/Applicants'
import './MessagesHeader.scss'
import Modal from 'Shared/components/Modal'
import DropdownMenu from 'Shared/components/DropdownMenu'
import { isArabicLayout } from 'Shared/helpers/layout'
import MembersListConnector from 'Chat/connectors/Components/MembersList/MembersListConnector'

class EmployersMessagesHeader extends Component {
  static propTypes = {
    jobOfferTitle: PropTypes.string.isRequired,
    candidateName: PropTypes.string.isRequired,
    candidateStatus: PropTypes.string.isRequired,
    candidateId: PropTypes.string.isRequired,
    jobOfferId: PropTypes.string.isRequired,
    applicationId: PropTypes.string,
    dismissed: PropTypes.bool.isRequired,
    dismissChat: PropTypes.func.isRequired,
    unblockChat: PropTypes.func.isRequired
  }

  state = {
    showDismissConfirmation: false
  }

  openDismissConfirmation = () => this.setState({ showDismissConfirmation: true })
  closeDismissConfirmation = () => this.setState({ showDismissConfirmation: false })

  handleDismiss = async () => {
    await this.props.dismissChat()
    return this.closeDismissConfirmation()
  }

  getCandidateLink = () => {
    const { applicationId, jobOfferId, candidateId } = this.props
    return applicationId
      ? `/employer/job-offer/${jobOfferId}/applicant/${applicationId}`
      : `/employer/candidate/${candidateId}`
  }

  returnToChannels = () => this.props.history.push('/employer/chat')

  render () {
    const { t, jobOfferTitle, candidateName, candidateStatus,
      applicationId, dismissed, unblockChat, jobOfferId } = this.props

    const applicantStatusClass = cx({
      'messages__applicant-status': true,
      'messages-mobile__applicant-status': true,
      'messages__applicant-status--success': candidateStatus === applicantStatuses.hired,
      'messages__applicant-status--error': candidateStatus === applicantStatuses.declined
    })

    const containerClass = cx({
      'messages-header-mobile': true,
      'messages-header-mobile--arabic': isArabicLayout
    })

    const dropdownmenuWebOptions = {
      triggerButton: <div className='messages-header-mobile__btn'><i className='messages-header-mobile__btn-more' /></div>,
      align: isArabicLayout ? 'left' : 'right',
      closeOnOutsideClick: true
    }

    const cleanJobOfferTitle = (title) => title.replace(/&amp;/ig, '&')

    return (
      <div className={containerClass}>
        <div className='messages-header-mobile__back' onClick={this.returnToChannels}>
          <i className='icon-im-arrow-forward' />
        </div>
        <div className='messages-header-mobile__content'>
          <div className='messages-header-mobile__title'>
            <div className='messages-header-mobile__name'>
              <Link to={this.getCandidateLink()} target='_blank'>
                <span>{candidateName}</span>
              </Link>
            </div>
            {
              applicationId && candidateStatus &&
              <div className='messages-header-mobile__status'>
                <div className={applicantStatusClass}>{t(`employer.applicants.applicantsType.${candidateStatus}`)}</div>
              </div>
            }
          </div>
          <div className='messages-header-mobile__subtitle'>
            {applicationId ? t('chat.applicantFor') : t('chat.candidateFor')}&nbsp;
            <Link to={`/jobs/${jobOfferId}`} target='_blank' className='messages__channel-link'>
              {cleanJobOfferTitle(jobOfferTitle)}
            </Link>
          </div>
        </div>
        <div className='messages-header-mobile__buttons'>
          <div>
            <MembersListConnector />
            <DropdownMenu {...dropdownmenuWebOptions}>
              {
                dismissed
                  ? <li className='messages-header-mobile__dropdown-item' onClick={unblockChat}>{t('chat.unblockChat')}</li>
                  : <li className='messages-header-mobile__dropdown-item' onClick={this.openDismissConfirmation}>{t('chat.dismissChat')}</li>
              }
            </DropdownMenu>
          </div>
          <Modal
            title={t('chat.dismissConfirmation.title')}
            handleSubmit={this.handleDismiss}
            handleClose={this.closeDismissConfirmation}
            show={this.state.showDismissConfirmation}
            handleCancel={this.closeDismissConfirmation}
            cancelText={t('common.cancel')}
            submitText={t('chat.dismissConfirmation.confirm')}
            showContact={false}
            showCancel
          >
            <p>{t('chat.dismissConfirmation.text')}</p>
          </Modal>
        </div>
      </div>
    )
  }
}

EmployersMessagesHeader.defaultProps = {
  jobOfferTitle: '',
  candidateName: '',
  candidateStatus: '',
  candidateId: '',
  jobOfferId: '',
  dismissed: false,
  dismissChat: () => {},
  unblockChat: () => {}
}

export default withRouter(withTranslations(EmployersMessagesHeader))



