import React from 'react'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { Link } from 'react-router-dom'
import { isArabicLayout } from 'Shared/helpers/layout'
import './MessagesHeader.scss'
import { toLower } from 'ramda'

const BotMessagesHeaderMobile = ({ t, userName, userRole, jobOfferTitle, jobOfferId, history }) => {
  const containerClass = cx({
    'messages-header-mobile': true,
    'messages-header-mobile--arabic': isArabicLayout
  })

  const returnToChannels = () => history.push(`/${userRole}/chat`)

  return (
    <div className={containerClass}>
      <div className='messages-header-mobile__back' onClick={returnToChannels}>
        <i className='icon-im-arrow-forward' />
      </div>
      <div className='messages-header-mobile__content'>
        <div className='messages-header-mobile__title'>
          <div className='messages-header-mobile__name'>
            {userName} {toLower(t('common.from'))} Oliv
          </div>
        </div>
        <Link className='messages-header-mobile__subtitle messages-header-mobile__subtitle-link' to={`/jobs/${jobOfferId}`} target='_blank'>
          <div>{jobOfferTitle}</div>
        </Link>
      </div>
    </div>
  )
}

export default withTranslations(BotMessagesHeaderMobile)



