import { connect } from 'react-redux'
import DayTimeline from 'Chat/components/DayTimeline/DayTimeline'
import { isFirstMessageOfTheDay, getMessageTimestamp } from 'Chat/selectors/messages'

const mapStateToProps = (state, ownProps) => ({
  isFirstMessageOfTheDay: isFirstMessageOfTheDay(state, ownProps.messageSid),
  date: getMessageTimestamp(state, ownProps.messageSid)
})

export default connect(mapStateToProps)(DayTimeline)



