import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { Link } from 'react-router-dom'
import { toLower } from 'ramda'
import './MessagesHeader.scss'

const BotMessagesHeader = ({ t, jobOfferTitle, userName, jobOfferId }) => (
  <div className='chat-view__header--messages-container'>
    <div className='chat-view__header chat-view__header--messages'>
      <div>
        <div>{userName} {toLower(t('common.from'))} Oliv</div>
        <Link className='messages__channel-description messages__channel-description--bot' to={`/jobs/${jobOfferId}`} target='_blank'>
          <div>{jobOfferTitle}</div>
        </Link>
      </div>
    </div>
  </div>
)

export default withTranslations(BotMessagesHeader)



