import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import ReactDOM from 'react-dom'
import Button from 'Shared/components/Button'
import MessagesLoader from 'Chat/components/MessagesLoader/MessagesLoader'
import EmployersMessagesHeaderConnector from 'Chat/connectors/Components/MessagesHeader/EmployersMessagesHeaderConnector'
import CandidatesMessagesHeaderConnector from 'Chat/connectors/Components/MessagesHeader/CandidatesMessagesHeaderConnector'
import BotMessagesHeaderConnector from 'Chat/connectors/Components/MessagesHeader/BotMessagesHeaderConnector'
import MessagesDefaultSection from 'Chat/components/MessagesDefaultSection/MessagesDefaultSection'
import BotClosableMessage from 'Chat/connectors/Components/BotClosableMessage/BotClosableMessageConnector'
import { Roles } from 'Shared/constants/User'
import './MessagesView.scss'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import { VISIBILITY_MESSAGES_LIMIT } from 'Chat/constants/defaultMessage'
import MessagesReceiverConnector from 'Chat/connectors/Components/Messages/MessagesReceiverConnector'
import MessagesSenderConnector from 'Chat/connectors/Components/Messages/MessagesSenderConnector'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import DayTimelineConnector from 'Chat/connectors/Components/DayTimeline/DayTimelineConnector'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import * as R from 'ramda'
import { Trans } from 'react-i18next'
import { ExternalRoutes } from 'Shared/constants/Routes'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import { getUniqMessagesByIndex } from 'Chat/helpers/messages'
import { isNotNil } from 'Shared/helpers/Ramda'

const getLastMessageIndex = R.pipe(
  R.last,
  R.pathOr(0, ['state', 'index'])
)

class Messages extends Component {
  static propTypes = {
    fetchApplication: PropTypes.func.isRequired,
    fetchMessages: PropTypes.func.isRequired,
    loadMoreMessages: PropTypes.func.isRequired,
    sendMessage: PropTypes.func.isRequired,
    channelSid: PropTypes.string.isRequired,
    userId: PropTypes.string.isRequired,
    userRole: PropTypes.string.isRequired,
    messagesCount: PropTypes.number.isRequired,
    applicationId: PropTypes.string,
    dismissed: PropTypes.bool.isRequired,
    messages: PropTypes.array.isRequired,
    impersonation: PropTypes.bool.isRequired
  }

  async componentDidMount () {
    const { applicationId, fetchApplication, fetchMessages } = this.props
    !!applicationId && fetchApplication(applicationId)
    await fetchMessages()
    if (this.props.match.params.channelSid) {
      this.props.showChannelMessages()
    }
    this.setState({ loadingMessages: false })
    this.scrollToBottom()

    if (this.props.isMobile) {
      document.getElementsByTagName('body')[0].style.overflow = 'hidden'
    }
  }

  async componentDidUpdate (prevProps) {
    const hasApplicationId = isNotNil(this.props.applicationId)
    const channelChangedInUrl = R.not(R.equals(prevProps.channelSid, this.props.channelSid))

    if (channelChangedInUrl) {
      hasApplicationId && this.props.fetchApplication(this.props.applicationId)
      this.setState({ loadingMessages: true })
      await this.props.fetchMessages(this.props.channelSid)
      this.setState({ loadingMessages: false })
      this.scrollToBottom()
    }

    const areMessagesLoaded = !this.state.loadingMessages
    const hasAtLeasOneMessage = prevProps.messages.length > 0

    if (areMessagesLoaded && hasAtLeasOneMessage && getLastMessageIndex(this.props.messages) > getLastMessageIndex(prevProps.messages)) {
      setTimeout(() => this.scrollToBottom())
    }
  }

  componentWillUnmount () {
    if (this.props.isMobile) {
      document.getElementsByTagName('body')[0].style.overflow = 'auto'
    }
  }

  state = {
    loadingMessages: true,
    newMessage: ''
  }

  getHeader = () => R.cond([
    [R.propEq('isOliviaBotChannel', true), () => <BotMessagesHeaderConnector />],
    [R.propEq('role', Roles.employer.name), () => <EmployersMessagesHeaderConnector />],
    [R.propEq('role', Roles.candidate.name), () => <CandidatesMessagesHeaderConnector />]
  ])({role: this.props.userRole, isOliviaBotChannel: this.props.isOliviaBotChannel})

  getDismissInformation = {
    [Roles.candidate.name]: this.props.t('chat.dismissed.candidate'),
    [Roles.employer.name]: this.props.t('chat.dismissed.employer')
  }

  static defaultProps = {
    messages: []
  }

  scrollToBottom = () => {
    const messagesContainer = ReactDOM.findDOMNode(this.messagesContainer)
    if (messagesContainer) {
      setTimeout(() => { messagesContainer.scrollTop = messagesContainer.scrollHeight }, 0)
    }
  }

  onMessageWrite = event => this.setState({ newMessage: event.target.value })
  sendMessage = async event => {
    event.preventDefault()
    const newMessage = this.state.newMessage
    this.setState({ newMessage: '' })
    return newMessage.length > 0 && this.props.sendMessage(newMessage)
  }

  onEnterPress = (e) => {
    if (e.keyCode === 13 && e.shiftKey === false) {
      e.preventDefault()
      return this.sendMessage(e)
    }
  }

  onLoadMore = () => this.props.loadMoreMessages(this.props.messages[0].index)

  getAvatarDetails = () => ({
    firstName: this.props.channelName,
    photoUrl: this.props.channelAvatar,
    avatarColour: this.props.avatarColour
  })

  getMessageInput = () => {
    const { t, dismissed, impersonation, isMobile, isCandidateAwaitingForEmployersReply, isOliviaBotChannel } = this.props

    const tooltipContent = <Trans i18nKey='chat.awaitingToReplyTooltip'>
      Awaiting employer reply. You will be able to continue this conversation once the employer
      replies to this chat thread. <a className='tooltip__link' href={ExternalRoutes.support} target='_blank'>Here</a> are some tips on the best ways to initiate chats on Oliv.
    </Trans>

    switch (true) {
      case dismissed || impersonation || isOliviaBotChannel:
        return (
          <div className='messages__send-form messages__send-form--disabled'>
            <input
              disabled
              className='messages__send-input'
              placeholder={t('chat.typeYourMessageHere')}
            />
            <button className='message__send-button message__send-button--loading'>
              <i className='icon-im-send' />
            </button>
          </div>
        )
      case isCandidateAwaitingForEmployersReply:
        return (
          <div className='messages__send-form'>
            <Tooltip content={tooltipContent} trigger={isMobile ? 'click' : 'mouseenter'}>
              <i className='ion-ios-information-circle form__tooltip' />
            </Tooltip>
            <input
              disabled
              className='messages__send-input'
              placeholder={''}
            />
            <button className='message__send-button message__send-button--loading'>
              <i className='icon-im-send' />
            </button>
          </div>
        )
      default:
        return (
          <form className='messages__send-form'>
            <textarea
              autoComplete='off'
              className='messages__send-input'
              name='message'
              rows={1}
              autoFocus
              onKeyDown={this.onEnterPress}
              placeholder={this.props.t('chat.typeYourMessageHere')}
              onChange={this.onMessageWrite}
              value={this.state.newMessage}
            />
            <button onClick={this.sendMessage} className='message__send-button'>
              <i className='icon-im-send' />
            </button>
          </form>
        )
    }
  }

  render () {
    const {
      t,
      userRole,
      messages,
      userId,
      channelName,
      messagesCount,
      dismissed,
      isOliviaBotChannel
    } = this.props

    const messagesViewClass = cx({
      'messages__container': true,
      'messages__container--show': this.props.isMessagesViewVisible,
      'messages__container--loading': R.isNil(this.props.channelName && R.isNil(this.props.channelAvatar))
    })

    const chatClass = cx({
      'messages__chat': true,
      'messages__chat--mobile': this.props.isMobile
    })

    const messagesListClass = cx({
      'messages__list': true,
      'messages__list--no-messages': messages.length === 0,
      'messages__list--default-message': userRole === Roles.candidate.name
    })

    const messagesSendInputClass = cx({
      'messages__send': true,
      'messages__send--mobile': this.props.isMobile
    })

    return (
      <div className={messagesViewClass}>
        {this.getHeader()}
        <div className={chatClass}>
          {this.state.loadingMessages ? <MessagesLoader /> : (
            <div className={messagesListClass} ref={(el) => { this.messagesContainer = el }}>
              {userRole === Roles.candidate.name && !isOliviaBotChannel && messagesCount <= VISIBILITY_MESSAGES_LIMIT && <MessagesDefaultSection />}
              <div>
                {messagesCount > messages.length && <div className='messages__show-more'>
                  <Button onClick={this.onLoadMore}>{t('chat.showPreviousMessages')}</Button>
                </div>}
                {
                  messages.length > 0
                    ? getUniqMessagesByIndex(messages).map(message => {
                      return <Fragment key={`${message.state.sid}-message`}>
                        <DayTimelineConnector key={`${message.state.sid}-timeline`} messageSid={message.state.sid} />
                        <BotClosableMessage key={`${message.state.sid}-bot-closable-message`} messageSid={message.state.sid} />
                        {
                          message.state.author === userId
                            ? <MessagesSenderConnector message={message} key={message.state.sid} />
                            : <MessagesReceiverConnector
                              key={message.state.sid}
                              scrollToBottom={this.scrollToBottom}
                              messageSid={message.state.sid}
                            />
                        }
                      </Fragment>
                    })
                    // TODO: Extract MessagesReceiverConnector into pure MessageReceiver component and Interview Invite Message Component
                    : <div>
                      <UserAvatar details={this.getAvatarDetails()} className='messages__avatar messages__avatar--empty-state' />
                      <div className='messages__empty-state-text'>
                        {t('chat.beginningOfConversation', {name: channelName})}
                      </div>
                    </div>
                }
              </div>
            </div>
          )}
          { dismissed && <p className='messages__dismissed'>{this.getDismissInformation[userRole]}</p>}
          <div className={messagesSendInputClass}>
            {this.getMessageInput()}
          </div>
        </div>
      </div>
    )
  }
}

export default withDeviceWidthDetector(withTranslations(Messages))



