import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { CandidatesMessagesHeader } from 'Chat/components/MessagesHeader'
import { getOrganisationName } from 'Shared/selectors/organisations'
import { hideChannelMessages } from 'Chat/ducks/chat'

const mapStateToProps = (state, ownProps) => {
  const { channelSid } = ownProps.match.params
  const {
    friendlyName,
    attributes: {
      organisationId,
      jobOfferId
    }
  } = state.chat.channels.entries[channelSid]

  return ({
    jobOfferTitle: friendlyName,
    organisationName: getOrganisationName(state, organisationId),
    jobOfferId
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  hideChannelMessages: () => dispatch(hideChannelMessages())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CandidatesMessagesHeader))



