import MembersListWeb from './MembersList'
import MembersListMobile from './MembersList.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const MembersList = props => props.isMobile ? <MembersListMobile {...props} /> : <MembersListWeb {...props} />

export default withDeviceWidthDetector(MembersList)



