import { t } from 'i18next'

export default values => {
  const errors = {}

  if (!values['employers']) {
    errors['employers'] = {
      key: 'errors.requiredField',
      options: {
        field: t('forms.teamMembersConversation.label')
      }
    }
  }

  return errors
}



