import React from 'react'
import Button from 'Shared/components/Button'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { Link, withRouter } from 'react-router-dom'
import './MessagesHeader.scss'

const CandidatesMessagesHeader = ({ t, jobOfferTitle, organisationName, jobOfferId, hideChannelMessages }) => (
  <div className='chat-view__header--messages-container'>
    <i className='icon-im-arrow-left messages__header--back-icon' onClick={hideChannelMessages} />
    <div className='chat-view__header chat-view__header--messages'>
      <div>
        <div>{jobOfferTitle}</div>
        <div className='messages__channel-description'>{organisationName}</div>
      </div>
      <div className='messages__header-buttons'>
        <Link className='messages__header-button--link' to={`/jobs/${jobOfferId}`} target='_blank'>
          <Button className='messages__header-button'>{t('chat.viewJobDetails')}</Button>
        </Link>
      </div>
    </div>
  </div>
)

CandidatesMessagesHeader.propTypes = {
  jobOfferTitle: PropTypes.string.isRequired,
  organisationName: PropTypes.string.isRequired,
  jobOfferId: PropTypes.string.isRequired,
  hideChannelMessages: PropTypes.func.isRequired
}

export default withRouter(withTranslations(CandidatesMessagesHeader))



