import { reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import validate from './AddTeamMembersMobileFormValidator'
import AddTeamMemberForm from 'Chat/forms/AddTeamMembers'
import { getAllOrganisationEmployersExceptSelf } from 'Shared/selectors/employers'
import { getUserOrganisationId } from 'Shared/selectors/user'
import { getChannelAdminUsers } from 'Chat/selectors/users'
import { getMobileAddMembersToConversationOptions, getMembersIdsList } from 'Chat/helpers/members'
import {
  getChannelsCandidateId,
  getChannelsUniqueName,
  getChannelsJobOfferId
} from 'Chat/selectors/channels'
import { joinChannel } from 'Chat/ducks/classes/channels'
import * as R from 'ramda'

export const formName = 'AddTeamMemberForm'

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  onSubmit: async (values, dispatch, ownProps) => {
    const isAlreadySelected = value => R.includes(value, ownProps.selectedEmployers)
    const employerIds = R.filter(R.pipe(isAlreadySelected, R.not))(values.employers)
    const { signature, candidateId, jobOfferId } = ownProps

    return dispatch(joinChannel(signature, { candidateId, employerIds, jobOfferId }))
  }
})(AddTeamMemberForm)

const mapStateToProps = (state, ownProps) => {
  const organisationId = getUserOrganisationId(state)
  const organisationEmployers = getAllOrganisationEmployersExceptSelf(state, organisationId)
  const members = getChannelAdminUsers(state, ownProps.match.params.channelSid)
  const selectedEmployers = getMembersIdsList(members)

  return ({
    options: getMobileAddMembersToConversationOptions(organisationEmployers),
    candidateId: getChannelsCandidateId(state, ownProps.match.params.channelSid),
    signature: getChannelsUniqueName(state, ownProps.match.params.channelSid),
    jobOfferId: getChannelsJobOfferId(state, ownProps.match.params.channelSid),
    selectedEmployers,
    initialValues: {
      employers: selectedEmployers
    }
  })
}

export default withRouter(connect(mapStateToProps)(Form))



