import React from 'react'
import PropTypes from 'prop-types'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import './Channel.scss'

const ChannelsWrapper = ({ isMobile, children, hasNotification }) => {
  return (
    <nav className='channel__wrapper' style={{ height: isMobile ? `calc(100vh - 99px)` : `calc(100vh - ${hasNotification ? 239 : 199}px)` }}>
      {children}
    </nav>
  )
}

ChannelsWrapper.propTypes = {
  children: PropTypes.node.isRequired
}

export default withDeviceWidthDetector(ChannelsWrapper)



