import { connect } from 'react-redux'
import { isValid, hasSubmitSucceeded, submit, isSubmitting } from 'redux-form'
import { withRouter } from 'react-router-dom'
import MembersList from 'Chat/components/MembersList'
import { formName } from 'Chat/connectors/Forms/AddTeamMembers/AddTeamMembersFormConnector'
import { getAllOrganisationEmployersExceptSelf } from 'Shared/selectors/employers'
import { getOrganisationEmployers } from 'Shared/ducks/employers'
import { getChannelAdminUsers } from 'Chat/selectors/users'

const mapStateToProps = (state, ownProps) => {
  return ({
    isAddTeamMembersFormValid: isValid(formName)(state),
    hasSubmitSucceeded: hasSubmitSucceeded(formName)(state),
    members: getChannelAdminUsers(state, ownProps.match.params.channelSid),
    teamMembers: getAllOrganisationEmployersExceptSelf(state, state.user.details.organisationId),
    isSubmitLoading: isSubmitting(formName)(state)
  })
}

const mapDispatchToProps = dispatch => ({
  submitAddTeamMembersForm: () => dispatch(submit(formName)),
  getOrganisationEmployers: () => dispatch(getOrganisationEmployers())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MembersList))



