import React, { Component, Fragment } from 'react'
import UserAvatar from 'Shared/components/UserAvatar'
import { ConfirmPopup } from 'Shared/components/Popup'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import * as R from 'ramda'
import { t } from 'i18next'
import AddTeamMemberFormConnector from 'Chat/connectors/Forms/AddTeamMembers/AddTeamMembersFormConnector'
import './MembersList.scss'
import NotificationService from 'Shared/services/NotificationService'

class MembersList extends Component {
  state = {
    isAddMemberModalOpen: false
  }

  componentDidMount () {
    this.props.getOrganisationEmployers()
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      this.closeModal()
      NotificationService.success(t('alerts.addTeamMemberToConversationSuccess'))
    }
  }

  openModal = () => this.setState({ isAddMemberModalOpen: true })
  closeModal = () => this.setState({ isAddMemberModalOpen: false })

  showMembersAvatarsList = members => members.map((member) => {
    const Button = <div className='members-list__avatar-conatiner'>
      <UserAvatar className='members-list__avatar' details={{ firstName: member.friendlyName, photoUrl: member.attributes.photoUrl, avatarColour: member.attributes.avatarColour }} />
    </div>

    return <div className='members-list__member' key={`list-${member.identity}`}>
      <ConfirmPopup
        position='bottom'
        align='center'
        withSubmit={false}
        withCancel={false}
        triggerButton={Button}
      >
        <div className='members-list__popup'>
          <span>{member.friendlyName}</span>
        </div>
      </ConfirmPopup>
    </div>
  })

  showMoreMembers = members => {
    const Button = <div className='members-list__show-more'>+{members.length}</div>

    return <div className='members-list__member members-list__member--show-more'>
      <ConfirmPopup
        position='bottom'
        align='center'
        withSubmit={false}
        withCancel={false}
        triggerButton={Button}
      >
        <div className='members-list__popup'>
          {this.showMoreMembersList(members)}
        </div>
      </ConfirmPopup>
    </div>
  }

  showMoreMembersList = members => members.map(member => (
    <div key={`show-more-${member.identity}`} className='members-list__show-more-item'>
      <UserAvatar details={{ firstName: member.friendlyName, photoUrl: member.attributes.photoUrl, avatarColour: member.attributes.avatarColour }} />
      <div className='members-list__show-more-name'>{member.friendlyName}</div>
    </div>
  ))

  get membersList () {
    const { members } = this.props
    if (members.length <= 3) return this.showMembersAvatarsList(members)
    if (members.length > 3) {
      return <Fragment>
        {this.showMembersAvatarsList(R.take(3, members))}
        {this.showMoreMembers(R.takeLast(members.length - 3, members))}
      </Fragment>
    }

    return null
  }

  get addMember () {
    const { isAddTeamMembersFormValid, submitAddTeamMembersForm, isSubmitLoading } = this.props

    return <div className='members-list__add-member'>
      <div className='members-list__add-member-button'>
        <BigJustIconButton onClick={this.openModal}><i className='icon-im-members' /></BigJustIconButton>
      </div>
      <Modal
        title={t('modals.addTeamMemberToConversation.title')}
        submitText={isSubmitLoading ? `${t('common.loading')}...` : t('modals.addTeamMemberToConversation.cta')}
        cancelText={t('common.cancel')}
        show={this.state.isAddMemberModalOpen}
        handleSubmit={submitAddTeamMembersForm}
        handleCancel={this.closeModal}
        handleClose={this.closeModal}
        closeOnSubmit={false}
        disableSubmitButton={!isAddTeamMembersFormValid || isSubmitLoading}
      >
        <div className='members-list__add-member-description'>
          {t('modals.addTeamMemberToConversation.description')}
        </div>
        <AddTeamMemberFormConnector />
      </Modal>
    </div>
  }

  render () {
    return (
      <div className='members-list__container'>
        {this.membersList}
        {this.addMember}
      </div>
    )
  }
}

export default MembersList



