import React from 'react'
import cx from 'classnames'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { withRouter } from 'react-router-dom'
import { isArabicLayout } from 'Shared/helpers/layout'
import DropdownMenu from 'Shared/components/DropdownMenu'
import './MessagesHeader.scss'

const CandidatesMessagesHeaderMobile = ({ t, jobOfferTitle, organisationName, jobOfferId, history }) => {
  const containerClass = cx({
    'messages-header-mobile': true,
    'messages-header-mobile--arabic': isArabicLayout
  })

  const returnToChannels = () => history.push('/candidate/chat')

  const dropdownmenuWebOptions = {
    triggerButton: <div className='messages-header-mobile__btn'><i className='ion-ios-more' /></div>,
    align: isArabicLayout ? 'left' : 'right',
    closeOnOutsideClick: true
  }

  const redirectToJobDetails = () => history.push(`/jobs/${jobOfferId}`)

  return (
    <div className={containerClass}>
      <div className='messages-header-mobile__back' onClick={returnToChannels}>
        <i className='icon-im-arrow-forward' />
      </div>
      <div className='messages-header-mobile__content'>
        <div className='messages-header-mobile__title'>
          <div className='messages-header-mobile__name'>
            {jobOfferTitle}
          </div>
        </div>
        <div className='messages-header-mobile__subtitle'>
          {organisationName}
        </div>
      </div>
      <div className='messages-header-mobile__buttons'>
        <div>
          <DropdownMenu {...dropdownmenuWebOptions}>
            <li className='messages-header-mobile__dropdown-item' onClick={redirectToJobDetails}>
              {t('chat.viewJobDetails')}
            </li>
          </DropdownMenu>
        </div>
      </div>
    </div>
  )
}

CandidatesMessagesHeaderMobile.propTypes = {
  jobOfferTitle: PropTypes.string.isRequired,
  organisationName: PropTypes.string.isRequired,
  jobOfferId: PropTypes.string.isRequired,
  hideChannelMessages: PropTypes.func.isRequired
}

export default withRouter(withTranslations(CandidatesMessagesHeaderMobile))



