import { connect } from 'react-redux'
import ShortlistedApplicantsList from 'Chat/components/ShortlistedApplicantsList/ShortlistedApplicantsList'
import { fetchMultipleCandidates } from 'Shared/ducks/candidates'
import { shortlistedCandidatesListFromMessage } from 'Chat/selectors/messages'

const mapStateToProps = (state, ownProps) => ({
  shortlistedCandidates: shortlistedCandidatesListFromMessage(state, ownProps.messageSid)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchMultipleCandidates: candidateIds => dispatch(fetchMultipleCandidates(ownProps.jobOfferId, candidateIds))
})

export default connect(mapStateToProps, mapDispatchToProps)(ShortlistedApplicantsList)



