import React, { useEffect } from 'react'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import { formatDate } from 'Shared/helpers/date'
import { nl2br } from 'Shared/helpers/string'
import { formats } from 'Shared/constants/Date'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations'
import './Message.scss'

const MessageSender = ({
  message, shouldShowDeliverInfo, updateLastViewedMessage,
  isLastMessage, messageIndex, isMessageRead, t }) => {
  const deliverLabel = () => isMessageRead ? t('chat.read') : t('chat.delivered')

  useEffect(() => {
    if (isLastMessage) {
      updateLastViewedMessage(messageIndex)
    }
  }, [])

  const deliverInfo = () => {
    const labelClass = cx({
      'message__deliver-label': true,
      'message__deliver-label--read': isMessageRead
    })

    return (
      <div className={labelClass}>
        <i className='icon-im-double-tick message__deliver-label-icon' />
        {deliverLabel()}
      </div>
    )
  }

  return (
    <div className='message message--sender'>
      <div className='message__datetime'>{ formatDate(message.state.timestamp, formats.time) }</div>
      <div className='message__content message__content--sender'>
        { parseHtmlWithoutSpan(nl2br(message.state.body)) }
      </div>
      {
        shouldShowDeliverInfo && deliverInfo()
      }
    </div>
  )
}
export default withTranslations(MessageSender)



