import React, { useState } from 'react'
import MobileWindow from 'Shared/components/MobileWindow/MobileWindow.mobile'
import './ChannelsListFilters.scss'
import { mapIndexed } from 'Shared/helpers/Ramda'
import { t } from 'i18next'
import { PrimaryButton } from 'Shared/components/Button'

function ChannelsListFiltersMobile (props) {
  const [filtersVisible, setFiltersVisibility] = useState(false)
  const [value, setValue] = useState('all')

  const openFilters = () => setFiltersVisibility(true)
  const closeFilters = () => setFiltersVisibility(false)

  const onSelectRadio = value => () => setValue(value)

  const reset = () => setValue('all')

  const handleBack = () => {
    reset()
    closeFilters()
  }

  const handleSubmit = () => {
    props.getSelectedValue(value)
    closeFilters()
  }

  const radioOption = (option, index) => (
    <div key={`filter-option-mobile-${index}`}>
      <label className='channels-list-filters-mobile__radio-label'>
        <span>{option.label} </span>
        <input
          className='channels-list-filters-mobile__radio'
          type='radio'
          onChange={onSelectRadio(option.value)}
          value={option.value}
          checked={value === option.value}
        />
        <span className='channels-list-filters-mobile__checkmark' />
      </label>
    </div>
  )

  const radioOptionList = () => mapIndexed(radioOption)(props.options)

  return (
    <div>
      <div onClick={openFilters} className='channels-list-filters-mobile__button'>
        <i className='icon-im-filters' />
        {value !== 'all' && <div className='chat-list-filters-mobile__button--counter'>1</div>}
      </div>
      <MobileWindow show={filtersVisible}>
        <div className='channels-list-filters-mobile__header'>
          <div className='channels-list-filters-mobile__title'>{t('common.filters')}</div>
          <div className='channels-list-filters-mobile__header-buttons'>
            <div onClick={handleBack} className='icon-im-arrow-forward channels-list-filters-mobile__back-button' />
            <div onClick={reset} className='channels-list-filters-mobile__reset-button'>{t('common.reset')}</div>
          </div>
        </div>
        <div className='channels-list-filters-mobile__content'>
          <fieldset>
            {radioOptionList()}
          </fieldset>
          <div className='channels-list-filters-mobile__filters-submit-button-container'>
            <PrimaryButton onClick={handleSubmit}>{t('common.applyFilter')}</PrimaryButton>
          </div>
        </div>
      </MobileWindow>
    </div>
  )
}

export default ChannelsListFiltersMobile



