import React, { Component } from 'react'
import { FormModalMobile } from 'Shared/components/Modal'
import withTranslations from 'Shared/hoc/withTranslations/index'
import AddTeamMembersFormConnector from 'Chat/connectors/Forms/AddTeamMembers/AddTeamMembersMobileFormConnector'
import NotificationService from 'Shared/services/NotificationService'
import { t } from 'i18next'

class MembersListMobile extends Component {
  state = {
    isTeamMembersListFormOpen: false
  }

  componentDidMount () {
    this.props.getOrganisationEmployers()
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      this.closeTeamMembersListForm()
      NotificationService.success(t('alerts.addTeamMemberToConversationSuccess'))
    }
  }

  triggerButton = () => (
    <div onClick={this.openTeamMembersListForm} className='members-list-mobile__btn'>
      {this.props.members.length > 0 && <div className='members-list-mobile__members-count'>{this.props.members.length}</div>}
      <i className='icon-im-members' />
    </div>
  )

  openTeamMembersListForm = () => this.setState({ isTeamMembersListFormOpen: true })
  closeTeamMembersListForm = () => this.setState({ isTeamMembersListFormOpen: false })

  teamMembersListForm = () => {
    const { t, submitAddTeamMembersForm, isAddTeamMembersFormValid, isSubmitLoading } = this.props
    return (
      <FormModalMobile
        title={t('modals.addTeamMemberToConversation.title')}
        show={this.state.isTeamMembersListFormOpen}
        handleClose={this.closeTeamMembersListForm}
        handleSubmit={submitAddTeamMembersForm}
        submitText={isSubmitLoading ? `${t('common.loading')}...` : t('modals.addTeamMemberToConversation.cta')}
        cancelText={t('common.cancel')}
        closeOnSubmit={false}
        disableSubmit={!isAddTeamMembersFormValid || isSubmitLoading}
      >
        <p className='members-list-mobile__modal-description'>{t('modals.addTeamMemberToConversation.description')}</p>
        <AddTeamMembersFormConnector shouldResetForm={!this.state.isTeamMembersListFormOpen} />
      </FormModalMobile>
    )
  }

  render () {
    return this.props.teamMembers.length > 0 ? (
      <div className='members-list-mobile'>
        {this.triggerButton()}
        {this.teamMembersListForm()}
      </div>
    ) : null
  }
}

export default withTranslations(MembersListMobile)



