import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import BotClosableMessage from 'Chat/components/BotClosableMessage'
import {
  isOliviaBotChannel,
  isBotDefaultMessageClosed
} from 'Chat/selectors/channels'
import { isFirstMessage } from 'Chat/selectors/messages'
import { updateChannelAttributes } from 'Chat/ducks/classes/channels'
import { isCandidate } from 'Shared/selectors/user'

const mapStateToProps = (state, ownProps) => {
  const {channelSid} = ownProps.match.params

  return ({
    isOliviaBotChannel: isOliviaBotChannel(state, channelSid),
    isFirstMessage: isFirstMessage(state, ownProps.messageSid),
    userRole: state.user.role,
    isBotDefaultMessageClosed: isBotDefaultMessageClosed(state, channelSid),
    disableClose: isCandidate(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  closeMessage: () => dispatch(updateChannelAttributes(ownProps.match.params.channelSid, { botDefaultMessageClosed: true }))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(BotClosableMessage))



