import React from 'react'
import UserAvatar from 'Shared/components/UserAvatar'
import '../MultiSelectList.scss'

const UserLogoOption = (props) => (
  <div className='team-member__select--value-with-logo'>
    <UserAvatar details={props.details} />
    <div className='team-member__select--value-label'>{props.label}</div>
  </div>
)

export default UserLogoOption



