import React, { Fragment } from 'react'
import cx from 'classnames'
import './ChannelFilterTag.scss'

function ChannelFilterTag (props) {
  const tagClass = cx({
    'channel-filter-tag': true,
    'channel-filter-tag--loading': props.isLoading,
    'channel-filter-tag--gold': props.tagColor === 'gold',
    'channel-filter-tag--primary': props.tagColor === 'primary',
    'channel-filter-tag--red': props.tagColor === 'red',
    'channel-filter-tag--green': props.tagColor === 'green'
  })

  return (
    <div className={tagClass}>
      { props.tagColor === 'gold' && <Fragment><i className='icon-im-star-circle' />&nbsp;</Fragment>}
      <span>{props.tagName}</span>
    </div>
  )
}

export default ChannelFilterTag



