import * as R from 'ramda'

export const getAddMembersToConversationOptions = (organisationUsers, conversationUsers) => {
  const conversationUsersIds = R.map(R.prop(['identity']))(conversationUsers)
  const usersToSelect = R.filter(
    R.pipe(
      organisationUser => R.includes(R.prop('id', organisationUser), conversationUsersIds),
      R.not
    )
  )(organisationUsers)

  return R.map(user => ({
    value: user.id,
    label: user.firstName + ' ' + user.lastName,
    photoUrl: user.photoUrl,
    firstName: user.firstName,
    avatarColour: user.avatarColour
  }))(usersToSelect)
}

export const getMobileAddMembersToConversationOptions = (organisationUsers) => {
  return R.map(user => ({
    value: user.id,
    label: user.firstName + ' ' + user.lastName,
    details: {
      photoUrl: user.photoUrl,
      firstName: user.firstName,
      avatarColour: user.avatarColour
    }
  }))(organisationUsers)
}

export const getMembersIdsList = R.map(R.prop('identity'))



