import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import MessagesSender from 'Chat/components/Message/MessageSender'
import {
  shouldShowDeliverInfo,
  isLastMessage,
  getMessageIndex,
  isMessageRead
} from 'Chat/selectors/messages'
import { updateLastViewedMessage } from 'Chat/ducks/classes/channels'

const mapStateToProps = (state, ownProps) => ({
  isMessageRead: isMessageRead(state, ownProps.message.state.sid),
  shouldShowDeliverInfo: shouldShowDeliverInfo(state, ownProps.message.state.sid),
  isLastMessage: isLastMessage(state, ownProps.message.state.sid),
  messageIndex: getMessageIndex(state, ownProps.message.state.sid)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  updateLastViewedMessage: index => dispatch(updateLastViewedMessage(ownProps.match.params.channelSid, index))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MessagesSender))



