import { connect } from 'react-redux'
import { Roles } from 'Shared/constants/User'
import ChannelsList from 'Chat/components/ChannelsList/ChannelsList'
import { getSortedChannelsWitAtLeastOneMessage, getSortedChannels, numberOfOlivPlusChannelsWithoutAnyResponse } from 'Chat/selectors/channels'
import { fetchLastMessageForEachChannel } from 'Chat/ducks/classes/channels'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import { isCandidateProfileApproved } from 'Shared/selectors/user'
import { hasOlivPlusMembership, neverHadAnyMembership } from 'Shared/selectors/membership'

const mapStateToProps = (state) => {
  const { role } = state.user
  const channelsMap = {
    [Roles.candidate.name]: getSortedChannelsWitAtLeastOneMessage(state),
    [Roles.employer.name]: getSortedChannels(state)
  }

  return ({
    sortedChannels: channelsMap[role],
    userRole: role,
    isCandidateApproved: isCandidateProfileApproved(state),
    hasOlivPlusMembership: hasOlivPlusMembership(state),
    numberOfOlivPlusChannelsWithoutAnyResponse: numberOfOlivPlusChannelsWithoutAnyResponse(state),
    neverHadAnyJobMembership: neverHadAnyMembership(state)
  })
}

const mapDispatchToProps = dispatch => ({
  fetchLastMessageForEachChannel: () => dispatch(fetchLastMessageForEachChannel())
})

export default withDeviceWidthDetector(connect(mapStateToProps, mapDispatchToProps)(ChannelsList))



