import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import MessageReceiver from 'Chat/components/Message/MessageReceiver'
import { getInterviewInvitationStatus } from 'Shared/selectors/jobOfferApplications'
import { isAnyTimeSlotChosen, chosenTimeSlot, chosenTimeSlotId } from 'Shared/selectors/jobOfferApplicationTimeSlots'
import { REQUEST_CHANGE_TIME_SLOT } from 'Shared/constants/JobOfferApplicationTimeSlots'
import {
  getMessageContent,
  getMessageTimestamp,
  getMessageUserIdentity,
  isMessageAnInterviewRequest,
  isCandidatesMessage,
  isOlivPlusMessage,
  botMessageType,
  messageRequiresYesOrNoAnswer,
  messageWithShortlistedCandidatesList,
  isChannelClosedByOlivia,
  isLastMessage,
  getMessageIndex
} from 'Chat/selectors/messages'
import {
  hasAttributesInTwilio,
  getUserFriendlyName,
  getUserPhotoUrlFromTwilio,
  getUserPhotoUrlFromDatabase,
  getUserAvatarColourFromTwilio,
  getUserAvatarColourFromDatabase,
  isOliviaBotUser
} from 'Chat/selectors/users'
import {
  getChannelApplicationId,
  getChannelOrganisationId,
  getChannelsJobOfferId,
  getChannelSignatureBySid,
  isOliviaBotChannel,
  isOliviaTyping
} from 'Chat/selectors/channels'
import { updateLastViewedMessage, updateChannelAttributes } from 'Chat/ducks/classes/channels'

const mapStateToProps = (state, ownProps) => {
  const { channelSid } = ownProps.match.params
  const applicationId = getChannelApplicationId(state, channelSid)
  const organisationId = getChannelOrganisationId(state, channelSid)
  const authorIdentity = getMessageUserIdentity(state, ownProps.messageSid)

  return ({
    userRole: state.user.role,
    interviewInvitationStatus: getInterviewInvitationStatus(state, applicationId),
    isAnyTimeSlotChosen: isAnyTimeSlotChosen(state, applicationId),
    chosenTimeSlot: chosenTimeSlot(state, applicationId),
    isRespondAChangeRequest: chosenTimeSlotId(state, applicationId) === REQUEST_CHANGE_TIME_SLOT,
    avatarDetails: ownProps.avatarDetails,
    isOliviaBotUser: isOliviaBotUser(state, authorIdentity),
    interviewRequest: isMessageAnInterviewRequest(state, ownProps.messageSid),
    authorFriendlyName: getUserFriendlyName(state, authorIdentity),
    authorPhotoUrl: hasAttributesInTwilio(state, authorIdentity)
      ? getUserPhotoUrlFromTwilio(state, authorIdentity)
      : getUserPhotoUrlFromDatabase(state, authorIdentity, organisationId),
    avatarColour: hasAttributesInTwilio(state, authorIdentity)
      ? getUserAvatarColourFromTwilio(state, authorIdentity)
      : getUserAvatarColourFromDatabase(state, authorIdentity, organisationId),
    messageContent: getMessageContent(state, ownProps.messageSid),
    messageTimestamp: getMessageTimestamp(state, ownProps.messageSid),
    isCandidatesMessage: isCandidatesMessage(state, ownProps.messageSid),
    isOlivPlusMessage: isOlivPlusMessage(state, ownProps.messageSid),
    // todo: tbd
    messageReadByEmployer: false,
    botMessageType: botMessageType(state, ownProps.messageSid),
    messageRequiresYesOrNoAnswer: messageRequiresYesOrNoAnswer(state, ownProps.messageSid),
    jobOfferId: getChannelsJobOfferId(state, channelSid),
    signature: getChannelSignatureBySid(state, channelSid),
    isOliviaBotChannel: isOliviaBotChannel(state, channelSid),
    applicationId,
    messageWithShortlistedCandidatesList: messageWithShortlistedCandidatesList(state, ownProps.messageSid),
    isChannelClosedByOlivia: isChannelClosedByOlivia(state, channelSid),
    isLastMessage: isLastMessage(state, ownProps.messageSid),
    messageIndex: getMessageIndex(state, ownProps.messageSid),
    isOliviaTyping: isOliviaTyping(state, channelSid)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  updateLastViewedMessage: index => dispatch(updateLastViewedMessage(ownProps.match.params.channelSid, index)),
  stopOliviaTyping: () => dispatch(updateChannelAttributes(ownProps.match.params.channelSid, { isOliviaTyping: false }))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(MessageReceiver))



