import React, { Component } from 'react'
import PropTypes from 'prop-types'
import InfiniteScroll from 'react-infinite-scroller'
import ChannelConnector from 'Chat/connectors/Components/Channel/ChannelConnector'
import { ChannelsWrapper } from 'Chat/components/Channel'
import withTranslations from 'Shared/hoc/withTranslations'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import OlivPlusBannerConnector from 'Candidate/connectors/OlivPlus/OlivPlusBannerConnector'
import OliviaBotBannerConnector from 'Employer/connectors/OliviaBotBanner/OliviaBotBannerConnector'
import ChannelsListFiltersConnector from 'Chat/connectors/Components/ChannelsListFilters/ChannelsListFiltersConnector'
import { filterValueMethods } from 'Chat/constants/channel'
import { Roles } from 'Shared/constants/User'
import { map, prop } from 'ramda'
import { notEquals } from 'Shared/helpers/Ramda'
import './ChannelsList.scss'

const getChannelsSidArray = map(prop('sid'))

class ChannelsList extends Component {
  state = {
    channelsIndexes: [],
    pageStart: 0,
    hasMoreChannels: true,
    loadingLastMessages: true,
    channelsList: this.props.sortedChannels
  }

  async componentDidMount () {
    // this is necessary for sorting, without that we can't sort channels by last message date...
    // because of Twilio... //@Sandra 26.02.2018
    await this.props.fetchLastMessageForEachChannel()
    this.setState({ loadingLastMessages: false })
    this.setState({ channelsList: this.props.sortedChannels })
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    const previousChannelsSids = getChannelsSidArray(prevProps.sortedChannels)
    const currentChannelsSids = getChannelsSidArray(this.props.sortedChannels)
    const areMessagesFetched = !this.state.loadingLastMessages

    if (areMessagesFetched && notEquals(previousChannelsSids)(currentChannelsSids)) {
      this.setState({
        channelsList: this.props.sortedChannels,
        channelsIndexes: [],
        pageStart: 0,
        hasMoreChannels: true
      })
    }
  }

  loadChannels = async () => {
    const items = this.state.channelsIndexes
    const allItemsCount = this.state.channelsList.length
    const currentItemsCount = items.length
    const itemsPerPage = 5
    const channelsIndexesToAdd = currentItemsCount + itemsPerPage < allItemsCount ? currentItemsCount + itemsPerPage : allItemsCount

    for (let i = currentItemsCount; i < channelsIndexesToAdd; i++) {
      items.push(i)
    }

    return this.setState({ channelsIndexes: items, pageStart: this.state.pageStart + 1, hasMoreChannels: currentItemsCount < allItemsCount })
  }

  get channels () {
    return this.state.channelsIndexes
      .map(channelIndex => {
        const channelSid = this.state.channelsList[channelIndex].sid

        return (
          <ChannelConnector key={channelSid} channelSid={channelSid} userRole={this.props.userRole} fetchedLastMessages={this.state.fetchedLastMessages} />
        )
      })
  }

  generateEmptyListContent = () => {
    const { isMobile, isCandidateApproved, hasOlivPlusMembership, userRole, t, neverHadAnyJobMembership } = this.props
    const isCandidate = userRole === Roles.candidate.name

    switch (true) {
      case isMobile && isCandidate && isCandidateApproved && !hasOlivPlusMembership:
        return <OlivPlusBannerConnector chatMobileView hideCloseMark />
      case isMobile && !isCandidate && !neverHadAnyJobMembership:
        return <OliviaBotBannerConnector className='channels-list__olivia-banner' disableClose />
      default:
        return t('chat.noChats')
    }
  }

  setFilter = filterBy => this.setState({
    channelsList: filterValueMethods[filterBy](this.props.sortedChannels),
    channelsIndexes: [],
    pageStart: 0,
    hasMoreChannels: true,
    loadingLastMessages: true
  })

  unreadPremiumMessagesNotification = () => {
    const { t, numberOfOlivPlusChannelsWithoutAnyResponse, userRole } = this.props

    return numberOfOlivPlusChannelsWithoutAnyResponse > 0 && userRole === Roles.employer.name && (
      <div className='channels-list__notification'>
        <i className='icon-im-reply channels-list__notification-icon' />
        <span>{t('chat.candidatesAwaitingToReply', { count: numberOfOlivPlusChannelsWithoutAnyResponse })}</span>
      </div>
    )
  }

  render () {
    const { numberOfOlivPlusChannelsWithoutAnyResponse, userRole } = this.props

    return (
      <div>
        <ChannelsListFiltersConnector getSelectedValue={this.setFilter} />
        {this.unreadPremiumMessagesNotification()}
        <ChannelsWrapper hasNotification={numberOfOlivPlusChannelsWithoutAnyResponse > 0 && userRole === Roles.employer.name} >
          <InfiniteScroll
            pageStart={this.state.pageStart}
            loadMore={this.loadChannels}
            hasMore={this.state.hasMoreChannels}
            useWindow={false}
          >
            {this.channels}
          </InfiniteScroll>
          {this.state.channelsList.length === 0 && <div className='chat-view__channels-empty-state'>
            {this.generateEmptyListContent()}
          </div>}
        </ChannelsWrapper>
      </div>
    )
  }
}

ChannelsList.propTypes = {
  sortedChannels: PropTypes.array.isRequired,
  fetchLastMessageForEachChannel: PropTypes.func.isRequired,
  userRole: PropTypes.string.isRequired
}

export default withDeviceWidthDetector(withTranslations(ChannelsList))



