import { connect } from 'react-redux'
import CandidateCard from 'Chat/components/CandidateCard/CandidateCard'
import {
  getCandidateDetails,
  getCandidateLocation,
  getCandidateFullName
} from 'Shared/selectors/candidates'
import { getCandidateMajorsList } from 'Shared/selectors/education'

const mapStateToProps = (state, ownProps) => {
  const candidateId = ownProps.candidateId
  return ({
    fullName: getCandidateFullName(state, candidateId),
    details: getCandidateDetails(state, candidateId),
    majors: getCandidateMajorsList(state, candidateId),
    location: getCandidateLocation(state, candidateId)
  })
}

export default connect(mapStateToProps)(CandidateCard)



