import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import TypingPlaceholder from 'Chat/components/TypingPlaceholder'
import { isOliviaTyping } from 'Chat/selectors/channels'

const mapStateToProps = (state, ownProps) => {
  const { channelSid } = ownProps.match.params

  return ({
    isOliviaTyping: isOliviaTyping(state, channelSid)
  })
}

export default withRouter(connect(mapStateToProps)(TypingPlaceholder))



