import React, { Fragment, PureComponent } from 'react'
import { compose } from 'redux'
import { Route } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import Loader from 'Shared/components/Loader/Loader'
import ChannelsListConnector from 'Chat/connectors/Components/ChannelsList/ChannelsListConnector'
import MessagesViewConnector from 'Chat/connectors/Views/MessagesViewConnector'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import withChatViewWrapper from './withChatViewWrapper'

import './ChatView.scss'
import { Helmet } from 'react-helmet'

class ChatView extends PureComponent {
  componentDidMount () {
    if (document.getElementsByClassName('footer-logged').length > 0) {
      document.getElementsByClassName('footer-logged')[0].style.display = 'none'
    }
  }

  componentWillUnmount () {
    if (document.getElementsByClassName('footer-logged').length > 0) {
      document.getElementsByClassName('footer-logged')[0].style.display = 'block'
    }
  }

  render () {
    const { t, userRole } = this.props

    return (
      <Fragment>
        <Helmet>
          <title>{t('meta.candidate.title.chat')}</title>
          <meta property='og:title' content={t('meta.candidate.title.chat')} />
        </Helmet>
        <PageTopHeaderMobile title={t('mobileBottomNavigation.messages')} />
        <div className='chat-view-mobile__container'>
          {
            this.props.loadingChannels ? <Loader className='loader--app' description={`${t('common.loading')}...`} /> : (
              <Fragment>
                <ChannelsListConnector />
                <div className='chat-view-mobile__channel-messages'>
                  {/* TODO: find a way to do it nicer */}
                  <Route path={`/${userRole}/chat/:channelSid`} component={MessagesViewConnector} />
                </div>
              </Fragment>
            )
          }
        </div>
      </Fragment>
    )
  }
}

export default compose(withChatViewWrapper, withTranslations)(ChatView)



