import { t } from 'i18next'
import * as R from 'ramda'

export default (values, props) => {
  const errors = {}

  const isAlreadySelected = value => R.includes(value, props.selectedEmployers)
  const employerIds = R.filter(R.pipe(isAlreadySelected, R.not))(values.employers)

  if (employerIds.length <= 0) {
    errors['employers'] = {
      key: 'errors.requiredField',
      options: {
        field: t('forms.teamMembersConversation.label')
      }
    }
  }

  return errors
}



