import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { BotMessagesHeader } from 'Chat/components/MessagesHeader'
import { hideChannelMessages } from 'Chat/ducks/chat'
import { getChannelNameFromTwilio } from 'Chat/selectors/channels'

const mapStateToProps = (state, ownProps) => {
  const { channelSid } = ownProps.match.params
  const {
    friendlyName,
    attributes: { jobOfferId }
  } = state.chat.channels.entries[channelSid]

  return ({
    jobOfferTitle: friendlyName,
    jobOfferId,
    userName: getChannelNameFromTwilio(state, channelSid),
    userRole: state.user.role
  })
}

const mapDispatchToProps = (dispatch) => ({
  hideChannelMessages: () => dispatch(hideChannelMessages())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(BotMessagesHeader))



