import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Roles } from 'Shared/constants/User'
import { hideChannelMessages } from 'Chat/ducks/chat'
import { fetchChannelsIfNeeded } from 'Chat/ducks/classes/channels'
import { transformToArray } from 'Shared/helpers/redux'
import ChatView from 'Chat/views/ChatView'
import { getSortedChannelsWitAtLeastOneMessage, getSortedChannels } from 'Chat/selectors/channels'
import { isMessagesViewVisible } from 'Chat/selectors/view'
import { receiveNewChannelMember } from 'Chat/ducks/classes/members'
import { hasOlivPlusMembership, neverHadAnyMembership } from 'Shared/selectors/membership'
import { isCandidateProfileApproved } from 'Shared/selectors/user'

const mapStateToProps = (state, ownProps) => {
  const { role } = state.user
  const channelsMap = {
    [Roles.candidate.name]: getSortedChannelsWitAtLeastOneMessage(state),
    [Roles.employer.name]: getSortedChannels(state)
  }

  return ({
    channels: transformToArray(state.chat.channels.entries),
    sortedChannels: channelsMap[role],
    userRole: role,
    noChannelSelected: ownProps.location.pathname === `/${role}/chat`,
    isMessagesViewVisible: isMessagesViewVisible(state),
    areChannelsFetched: state.chat.areChannelsFetched,
    isCandidateApproved: isCandidateProfileApproved(state),
    hasOlivPlusMembership: hasOlivPlusMembership(state),
    neverHadAnyJobMembership: neverHadAnyMembership(state)
  })
}

const mapDispatchToProps = dispatch => ({
  fetchChannels: () => dispatch(fetchChannelsIfNeeded()),
  hideChannelMessages: () => dispatch(hideChannelMessages()),
  receiveNewChannelMember: () => dispatch(receiveNewChannelMember())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ChatView))



