import AddTeamMemberFormWeb from 'Chat/forms/AddTeamMembers/AddTeamMembersForm'
import AddTeamMemberFormMobile from 'Chat/forms/AddTeamMembers/AddTeamMembersForm.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const AddTeamMembersForm = (props) => props.isMobile ? <AddTeamMemberFormMobile {...props} /> : <AddTeamMemberFormWeb {...props} />

export default withDeviceWidthDetector(AddTeamMembersForm)



