import ChatViewWeb from './ChatView'
import ChatViewMobile from './ChatView.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const ChatView = (props) => props.isMobile ? <ChatViewMobile {...props} /> : <ChatViewWeb {...props} />

export default withDeviceWidthDetector(ChatView)



