import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { NavLink } from 'react-router-dom'
import { pathOr, pipe, equals, isNil, any, and } from 'ramda'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import { formats } from 'Shared/constants/Date'
import { formatChatMessageTimestamp } from 'Shared/helpers/date'
import { shortenText } from 'Shared/helpers/string'
import { hasUnreadMessages, countUnreadMessages } from 'Chat/ChatHelper'
import './Channel.scss'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import ChannelFilterTagConnector from 'Chat/connectors/Components/ChannelFilterTag/ChannelFilterTagConnector'
import { convertHtmlToPlainText } from 'Shared/helpers/editor'

class Channel extends Component {
  static propTypes = {
    channel: PropTypes.object.isRequired,
    lastMessage: PropTypes.object,
    showChannelMessages: PropTypes.func.isRequired,
    loadingLastMessages: PropTypes.bool.isRequired
  }

  hasChannelNameEmpty = () => isNil(this.props.channelName)
  hasChannelAvatarDetailsEmpty = () => and(isNil(this.props.channelAvatar), isNil(this.props.avatarColour))
  isLoadingChannelDetails = () => any(
    equals(true),
    [
      this.hasChannelNameEmpty(),
      this.hasChannelAvatarDetailsEmpty()
    ]
  )

  state = {
    loadingChannelDetails: this.isLoadingChannelDetails()
  }

  async componentDidMount () {
    if (this.state.loadingChannelDetails) {
      await this.props.getChannelMembers()
      await this.props.getUserDescriptors()

      if (!this.props.hasAttributesInTwilio) {
        this.props.fetchDetails(this.props.channel)
      }

      this.setState({ loadingChannelDetails: false })
      this.props.receiveChannelDismissStatusChange()
    }
  }

  componentDidUpdate (prevProps) {
    if (!this.props.hasAttributesInTwilio && this.props.hasAttributesInTwilio !== prevProps.hasAttributesInTwilio) {
      this.props.fetchDetails(this.props.channel)
    }
  }

  getAvatarDetails = () => ({
    firstName: this.props.channelName,
    photoUrl: this.props.channelAvatar,
    avatarColour: this.props.avatarColour
  })

  render () {
    const { loadingChannelDetails } = this.state
    const { t, isSelected, channel, lastMessage, userRole, channelName, userId, isOliviaBotChannel } = this.props
    const isLastMessageAuthor = pipe(
      pathOr('', ['state', 'author']),
      equals(userId)
    )(lastMessage)
    const { lastConsumedMessageIndex, messagesCount } = channel
    const isChannelLoading = loadingChannelDetails
    const hasUnreadState = !isLastMessageAuthor && hasUnreadMessages(lastConsumedMessageIndex, messagesCount)

    const channelClass = cx({
      'channel-link': true,
      'channel-link--active': isSelected,
      'channel-link--unread': !isSelected && hasUnreadState,
      'channel-link--loading': isChannelLoading
    })

    const titleClass = cx({
      'channel__title': true,
      'channel__title--unread': hasUnreadState
    })
    const positionClass = cx({
      'channel__position': true,
      'channel__position--unread': hasUnreadState
    })

    return (
      <div style={{ minHeight: '72px' }}>
        <NavLink
          to={`/${userRole}/chat/${channel.sid}`}
          className={channelClass}
        >
          <div className='channel__container'>
            <div className='columns'>
              <div className='col-2'>
                <UserAvatar details={this.getAvatarDetails()} icon={isOliviaBotChannel && 'icon-im-oliv-logo'} />
              </div>
              <div className='col-7'>
                {
                  !isOliviaBotChannel && <ChannelFilterTagConnector channelSid={channel.sid} isLoading={isChannelLoading} />
                }
                <div className={titleClass}>{ channelName ? shortenText(channelName, 30) : <span>&nbsp;</span> }</div>
                <div className={positionClass}>{shortenText(channel.friendlyName, 30)}</div>
                <div className='channel__message'>{ lastMessage ? shortenText(convertHtmlToPlainText(lastMessage.state.body, ' '), 30) : t('chat.noMessagesYet') }</div>
              </div>
              <div className='col-3 channel__aside'>
                <div className='channel__message-timestamp'>{ lastMessage && formatChatMessageTimestamp(lastMessage.state.timestamp, formats.dashed) }</div>
                {
                  !isSelected &&
                  hasUnreadState &&
                  !isChannelLoading &&
                  <div className='channel__badge'>{ countUnreadMessages(lastConsumedMessageIndex, messagesCount) }</div> }
              </div>
            </div>
          </div>
        </NavLink>
      </div>
    )
  }
}

export default withTranslations(Channel)



