import React from 'react'
import './MessagesDefaultSection.scss'
import Button from 'Shared/components/Button/index'
import withTranslations from 'Shared/hoc/withTranslations'
import { ExternalRoutes } from 'Shared/constants/Routes'

const MessagesDefaultSection = ({ t }) => {
  const openInterviewTips = () => window.open(ExternalRoutes.interviewTips, '_blank')

  return (
    <div className='messages-default-section__container'>
      <h1 className='messages-default-section__header'>{t('chat.defaultMessage.title')}</h1>
      <p className='messages-default-section__description'>{t('chat.defaultMessage.description')}</p>
      <Button className='messages-default-section__cta-btn' primary onClick={openInterviewTips}>
        {t('chat.defaultMessage.ctaButton')}
      </Button>
    </div>
  )
}

export default withTranslations(MessagesDefaultSection)



