import CandidatesMessagesHeaderWeb from './CandidatesMessagesHeader'
import CandidatesMessagesHeaderMobile from './CandidatesMessagesHeader.mobile'
import EmployersMessagesHeaderWeb from './EmployersMessagesHeader'
import EmployersMessagesHeaderMobile from './EmployersMessagesHeader.mobile'
import BotMessagesHeaderWeb from './BotMessagesHeader'
import BotMessagesHeaderMobile from './BotMessagesHeader.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const EmployersMessagesHeaderComponent = (props) => props.isMobile ? <EmployersMessagesHeaderMobile {...props} /> : <EmployersMessagesHeaderWeb {...props} />
export const EmployersMessagesHeader = withDeviceWidthDetector(EmployersMessagesHeaderComponent)

const CandidatesMessagesHeaderComponent = (props) => props.isMobile ? <CandidatesMessagesHeaderMobile {...props} /> : <CandidatesMessagesHeaderWeb {...props} />
export const CandidatesMessagesHeader = withDeviceWidthDetector(CandidatesMessagesHeaderComponent)

const BotMessagesHeaderComponent = (props) => props.isMobile ? <BotMessagesHeaderMobile {...props} /> : <BotMessagesHeaderWeb {...props} />
export const BotMessagesHeader = withDeviceWidthDetector(BotMessagesHeaderComponent)



